<template>
    <div>
        <!--  -->
        <el-form :inline="true" :model="pageparm" class="user-search" ref="searchForm">
            <el-form-item label="" prop="result">
                <el-select v-model="pageparm.result" placeholder="" @change="changeInput($event)">
                    <el-option label="" value=""></el-option>
                    <el-option label="" value=""></el-option>
                    <el-option label="" value=""></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-col :span="11">
                    <el-date-picker v-model="timeList" type="daterange" range-separator="" start-placeholder=""
                        end-placeholder="">
                    </el-date-picker>
                </el-col>
            </el-form-item>
            <el-form-item label="" prop="carNumber">
                <el-select multiple filterable v-model="carNumbers" placeholder="" @change="changeInput($event)">
                    <el-option v-for="item in carNumberList" :key="item.value" :label="item.value" :value="item.value">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="carBodyNumber">
                <el-input size="small" v-model="pageparm.carBodyNumber" placeholder="" @input="changeInput($event)"></el-input>
            </el-form-item>
            <el-form-item label="" prop="faultDescription">
                <el-input size="small" v-model="pageparm.faultDescription" placeholder="" @input="changeInput($event)"></el-input>
            </el-form-item>
            <el-form-item label="" prop="source">
                <el-input size="small" v-model="pageparm.source" placeholder="" @input="changeInput($event)"></el-input>
            </el-form-item>
            <el-form-item label="" prop="method">
                <el-input size="small" v-model="pageparm.method" placeholder="" @input="changeInput($event)"></el-input>
            </el-form-item>
            <el-form-item label="" prop="teamsGroups">
                <el-select v-model="pageparm.teamsGroups" placeholder="" @change="changeInput($event)">
                    <el-option v-for="item in teamsList" :key="item.id" :label="item.name" :value="item.name">
                    </el-option>
                </el-select>
            </el-form-item>
            <br>
            <el-form-item>
                <el-button size="small" type="primary" icon="el-icon-search" @click="search()"></el-button>
                <el-button size="small" type="primary" icon="el-icon-refresh-right"
                    @click="resetForm('searchForm')"></el-button>
                <el-button size="small" type="primary" icon="el-icon-plus" @click="handleEdit()"></el-button>
                <el-button size="small" type="warning" icon="el-icon-star-off" @click="exportExcel()"></el-button>
            </el-form-item>
        </el-form>

        <!--  -->
        <el-table :data="tableData" style="width: 100%" border height="600">
            <el-table-column type="index" label="" align="center" width="130" :index="indexMethod"
                fixed></el-table-column>
            <el-table-column prop="deliveryDate" label="" width="180" align="center"></el-table-column>
            <el-table-column prop="carNumber" label="" width="180" align="center"></el-table-column>
            <el-table-column prop="carBodyNumber" label="" align="center"></el-table-column>
            <el-table-column prop="faultDescription" label="" width="180" align="center"></el-table-column>
            <el-table-column prop="teamsGroups" label="" width="180" align="center"></el-table-column>
            <el-table-column prop="result" label="" width="130" align="center"></el-table-column>
            <el-table-column prop="method" label="" width="180" align="center"></el-table-column>
            <el-table-column prop="source" label="" align="center">
                <template slot-scope="scope">
                    {{ scope.row.source == 'null' ? '' : scope.row.source }}
                </template>
            </el-table-column>
            <el-table-column prop="notes" label="" align="center" width="160">
                <template slot-scope="scope">
                    {{ scope.row.notes == 'null' ? '' : scope.row.notes }}
                </template>
            </el-table-column>

            <el-table-column prop="" label="" align="center">
                <template slot-scope="scope">
                    <el-button type="danger" icon="el-icon-delete" circle @click="deleteById(scope.row)"></el-button>
                </template>
            </el-table-column>

            <el-table-column prop="notes" label="" align="center" width="130">
              <template slot-scope="scope">
                <el-button @click="showModal(scope.row.id)" class="icon-button">
                  <i class="el-icon-view"></i>
                  
                </el-button>
              </template>
              <el-dialog
                title=""
                :visible.sync="dialogVisible"
                width="40%"
                :before-close="handleCloseFile"
                append-to-body
              >
                <el-table :data="attachmentList" style="width: 100%">
                  <el-table-column prop="fileName" align="center" label="">
                    <template slot-scope="{ row }">
                      <el-link @click="handleFileNameClick(row)">{{ row.fileName }}</el-link>
                    </template>
                  </el-table-column>
                  <el-table-column prop="fileType" align="center" label=""></el-table-column>
                  <el-table-column label="" align="center">
                    <template slot-scope="{ row }">
                      <el-button type="primary" icon="el-icon-download" @click="downloadAttachment(row)"></el-button>
                      <el-button type="danger" icon="el-icon-delete" @click="deleteAttachment(row)"></el-button>
                    </template>
                  </el-table-column>
                </el-table>
              </el-dialog>

            </el-table-column>
            <el-table-column prop="" label="" width="120" align="center" fixed="right">
                <template slot-scope="scope">
                    <div style="margin-top: 5px;">
                        <!--  -->
                        <el-button @click="showUploadModal(scope.row)" size="small" type="primary"></el-button>
                    </div>
                </template>
            </el-table-column>

            <el-table-column prop="" label="" width="180" align="center" fixed="right">
                <template slot-scope="scope">
                    <el-button type="primary" icon="el-icon-edit" circle
                        @click="handleEdit(scope.$index, scope.row)"></el-button>
                    <!-- <el-button type="danger" icon="el-icon-delete" circle @click="deleteById(scope.row)"></el-button> -->
                    <el-button type="success" icon="el-icon-bottom" circle
                        @click="changState(scope.row.id, scope.row.state)"></el-button>
                </template>
            </el-table-column>
        </el-table>

        <Pagination v-bind:child-msg="pageparm" v-bind:total="total" @callFather="callFather"></Pagination>

        <!--  -->
        <el-dialog :title="title" :visible.sync="editFormVisible" width="55%" @click="closeDialog('editForm')">
            <el-form label-width="120px" :model="editForm" :rules="rules" ref="editForm">
                <el-form-item label="" prop="deliveryDate">
                    <el-date-picker value-format="yyyy-MM-dd" v-model="editForm.deliveryDate" type="date"
                        placeholder="">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="" prop="carNumber">
                    <el-input v-model="editForm.carNumber" auto-complete="off" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="carBodyNumber">
                    <el-input v-model="editForm.carBodyNumber" :min="1" label="" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="faultDescription">
                    <el-input type="textarea" v-model="editForm.faultDescription" label="" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="teamsGroups">
                    <el-select v-model="editForm.teamsGroups" placeholder="">
                        <el-option v-for="item in teamsList" :key="item.id" :label="item.name" :value="item.name">
                        </el-option>
                    </el-select>
                    <el-link type="primary" href="http://localhost:8080/#/teams/index" :underline="false"></el-link>
                </el-form-item>
                <el-form-item label="" prop="result">
                    <label slot="label"><span style="color:red">*</span>&nbsp;</label>
                    <el-radio-group v-model="result" @change="changeResult">
                        <el-radio class="radio" label=""></el-radio>
                        <el-radio class="radio" label=""></el-radio>
                        <el-radio class="radio" label="">()</el-radio>
                    </el-radio-group>
                    <el-input v-if="result == ''" v-model="editForm.result" label="" placeholder=""
                        size="mini"></el-input>
                </el-form-item>
                <el-form-item label="" prop="method">
                    <el-input type="textarea" size="small" v-model="editForm.method" auto-complete="off"
                        placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="respDepart">
                    <el-input v-model="editForm.source" label="" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="node">
                    <el-input size="small" v-model="editForm.notes" auto-complete="off" placeholder=""></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button size="small" @click="closeDialog('editForm')"></el-button>
                <el-button size="small" type="primary" class="title" @click="handelFault('editForm')"></el-button>
            </div>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="uploadModalVisible" width="650px" :before-close="beforeClose" append-to-body
         :show-close="false" :close-on-click-modal="false">
            <el-upload class="uploadFile" :action="`${baseURL}/fault/upload`" :on-success="handleFileSuccess"
                :on-error="handleFileError" :file-list="fileList" :data="fault" ref="refName" drag multiple>
                <i class="el-icon-upload"></i>
                <div class="el-upload__text"><em></em></div>
            </el-upload>
            <div slot="footer" class="dialog-footer">
                <el-button @click="upload"> </el-button>
                <el-button type="primary" @click="upload"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>
  
<script>
import axios from 'axios'
import Pagination from '../../../components/Pagination.vue'
import { baseURL } from '../../../api/base'
import { carNumberList } from '../.././../common/carNumber'
import {downloadFile} from '../../../api/FileDownload'


export default {
    name: 'Fault',
    components: {
        Pagination
    },
    data() {
        return {
            carNumbers: [],
            carNumberList,
            baseURL,
            timeList: [],
            tableData: [],
            editFormVisible: false,
            title: '',
            editForm: {
                risk: 0,
                state: 1
            },
            risk: 0,
            result: '',
            rules: {
                deliveryDate: [{ required: true, message: '', trigger: 'blur' }],
                carNumber: [{ required: true, message: '', trigger: 'blur' }],
                carBodyNumber: [{ required: true, message: '', trigger: 'blur' }],
                faultDescription: [{ required: true, message: '', trigger: 'blur' }],
                teamsGroups: [{ required: true, message: '', trigger: 'blur' }],
                funClass: [{ required: true, message: '', trigger: 'blur' }],
                handle: [{ required: true, message: '', trigger: 'blur' }],
            },
            pageparm: {
                pageNo: 1,
                pageSize: 10000,
                state: 1
            },
            total: 0,
            allList: [],
            headers: "'Content-Type': 'multipart/form-data'",
            loading: true,
            teamsList: [],

            uploadModalVisible: false,
            fileList: [],
            dialogVisible: false,
            attachmentList: [
                // 
                // { name: '1', type: '', url: '' }
            ],
            previewDialogVisible: false,
            previewFile: '',
            fault: {},
            deleteParams: {},
        }
    },
    created() {
        this.getFault()
        this.getTeams()
    },
    computed: {
        indexMethod: function () {
            return (this.pageparm.pageNo - 1) * (this.pageparm.pageSize) + 1
        }
    },
    methods: {
        changeInput(e) {
            this.$forceUpdate();
        },
        // 
        resetForm(formName) {
            this.$refs[formName].resetFields();
            this.timeList = []
            delete this.pageparm.startTime
            delete this.pageparm.endTime
            if (this.pageparm.carNumbers) {
                delete this.pageparm.carNumbers
            }
            this.carNumbers = []
        },
        getTeams() {
            axios({
                url: baseURL + '/teams/selectall'
            }).then(res => {
                this.teamsList = res.data.data
                this.loading = false
            }).catch(err => {
                console.log(err);
            })
        },
        search() {
            if (this.carNumbers.length) {
                this.pageparm.carNumbers = this.carNumbers
            }
            this.loading = true
            if (this.timeList.length) {
                this.pageparm.startTime = this.getYMDHMS(new Date(this.timeList[0].toString()));
                this.pageparm.endTime = this.getYMDHMS(new Date(this.timeList[1].toString()))
            }
            this.pageparm.state = 1
            this.pageparm.pageNo = 1
            this.pageparm.pageSize = 10000
            axios({
                method: 'post',
                url: baseURL + '/fault/screenfault',
                data: this.pageparm
            }).then(res => {
                this.$message.success('')
                this.tableData = res.data.data.list
                this.tableData = this.tableData.map(item => {
                    item.deliveryDate = this.getYMDHMS(item.deliveryDate)
                    item.risk = item.risk == 'null' || '0' ? 0 : parseInt(item.risk)
                    item.code = item.code == 'null' ? '' : item.code
                    item.schedule = item.schedule == 'null' ? '' : item.schedule
                    item.respDepart = item.respDepart == 'null' ? '' : item.respDepart
                    item.notes = item.notes == 'null' ? '' : item.notes
                    return item
                })
                this.total = res.data.data.size
                this.loading = false
            }).catch(err => {
                this.initPage()
                this.getFault()
                this.$message.error('')
                console.log(err)
            })

        },
        initPage() {
            this.pageparm = {
                pageNo: 1,
                pageSize: 10000,
            }
        },
        getYMDHMS(val) {
            let date = new Date(val)
            let Y = date.getFullYear()
            const M = (date.getMonth() + 1).toString().padStart(2, '0')
            const D = (date.getDate()).toString().padStart(2, '0')
            return Y + "-" + M + "-" + D;
        },
        exportExcel() {
            if (this.carNumbers.length) {
                this.pageparm.carNumbers = this.carNumbers
            }
            if (this.timeList.length) {
                this.pageparm.startTime = this.getYMDHMS(new Date(this.timeList[0].toString()));
                this.pageparm.endTime = this.getYMDHMS(new Date(this.timeList[1].toString()))
            }

            let openUrl = `${baseURL}/fault/exportsenior?`;
            for (let obj in this.pageparm) {
                if (this.pageparm[obj]) {
                    openUrl += obj + '=' + this.pageparm[obj] + '&'
                }
            }
            axios.get(openUrl).then(res => {
                console.log(openUrl)
                window.location.href = openUrl
            })

        },
        callFather(val) {
            this.pageparm.pageNo = val.pageNo
            this.pageparm.pageSize = val.pageSize
            this.getFault()
        },
        getFault() {
            if (this.carNumbers.length) {
                this.pageparm.carNumbers = this.carNumbers
            }
            this.pageparm.state = 1
            axios({
                url: baseURL + '/fault/screenfault',
                method: 'post',
                data: this.pageparm
            }).then(res => {
                this.tableData = res.data.data.list
                this.tableData = this.tableData.map(item => {
                    item.deliveryDate = this.getYMDHMS(item.deliveryDate)
                    item.risk = item.risk == 'null' || '0' ? 0 : parseInt(item.risk)
                    item.code = item.code == 'null' ? '' : item.code
                    item.schedule = item.schedule == 'null' ? '' : item.schedule
                    item.respDepart = item.respDepart == 'null' ? '' : item.respDepart
                    item.notes = item.notes == 'null' ? '' : item.notes
                    return item
                })
                this.total = res.data.data.size
                this.loading = false
            }).catch(err => {
                console.log(err);
            })
        },
        deleteById(row) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                axios({
                    url: baseURL + '/fault/deletefaultid',
                    params: { id: row.id }
                }).then(res => {
                    this.$message({
                        message: '',
                        type: 'success'
                    });
                    this.search()
                }).catch(err => {
                    console.log(err)
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        handleEdit(index, row) {
            this.editFormVisible = true
            if (row != undefined && row != 'undefined') {
                this.title = ''
                this.editForm = row
                if (this.editForm.result != '' & this.editForm.result != '') {
                    this.result = ''
                } else if (this.editForm.result == '') {
                    this.result = ''
                } else if (this.editForm.result == "") {
                    this.result = ''
                }
            } else {
                this.title = ''
                this.editForm = { state: 1 }
            }
        },
        changeResult() {
            this.editForm.result = ''
        },
        handelFault() {
            this.$refs['editForm'].validate((valid) => {
                if (valid) {
                    if (this.result != '') {
                        this.editForm.result = this.result
                    }
                    this.editForm.risk = this.risk
                    if (this.title == '') {
                        axios({
                            url: baseURL + '/fault/insertfault',
                            method: 'post',
                            data: this.editForm
                        }).then(res => {
                            this.$message({
                                message: this.title + '',
                                type: 'success'
                            });
                            this.editFormVisible = false
                            this.editForm = {}
                            this.initPage()
                            this.getFault()
                        }).catch(err => {
                            console.log(err)
                            this.$message({
                                message: this.title + '',
                                type: 'error'
                            });
                            this.editFormVisible = false
                            this.editForm = {}
                            this.initPage()
                            this.getFault()
                        })
                    } else {
                        for (let obj in this.editForm) {
                            if (!this.editForm[obj] & obj != 'risk') {
                                this.editForm[obj] = 'null'
                            }
                        }
                        axios({
                            url: baseURL + '/fault/updatefaultid',
                            method: 'post',
                            data: this.editForm,
                        }).then(res => {
                            this.editFormVisible = false
                            this.editForm = {}
                        }).catch(err => {
                            console.log(err)
                        })
                    }
                } else {
                    console.log('');
                    return false;
                }
            });
        },
        closeDialog() {
            this.editFormVisible = false
            this.editForm = {}
            this.initPage()
            this.getFault()
        },
        handleUploadSuccess(res) {
            this.uploadUrlPath = JSON.stringify(res);
            this.loading.close();
        },
        handleBeforeUpload() {
            this.loading = this.$loading({
                lock: true,
                text: "",
                background: "rgba(0, 0, 0, 0.7)",
            });
        },
        handleUploadError() {
            this.$message({
                type: "error",
                message: "",
            });
            this.loading.close();
        },
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePreview(file) {
            console.log(file);
        },
        handleExceed(files, fileList) {
            this.$message.warning(` ${files.length}  ${files.length + fileList.length} `);
        },
        beforeRemove(file, fileList) {
            return this.$confirm(` ${file.name}`);
        },
        handleSuccess() {
            this.getFault()
            this.$message.success("")
        },
        handleError() {
            this.getFault()
            this.$message.error("")
        },
        resetRisk() {
            this.risk = 0
        },
        changState(id, state) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                axios({
                    url: ` ${baseURL}/fault/changestate`,
                    params: { id, state: 0 }
                }).then(res => {
                    this.$message.success("")
                    this.search()
                }).catch(err => {
                    this.$message.error('')
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        showModal(id) {
          // 
          axios({
            url: baseURL + '/fault/fileList/' + id,
            method: 'get'
          }).then(res => {
            console.log(res)
            this.dialogVisible = true;
            this.attachmentList = res.data.data || []
          })
        },
        handleCloseFile(done) {
          // 
          done(); //  done() 
        },
        // 
        showUploadModal(row) {
            this.fault.id = row.id;
            this.uploadModalVisible = true;
        },
         // 
         beforeClose(done) {
          // 
          done();
        },
        // 
        upload() {
          // 
          this.$refs.refName.clearFiles();
          this.uploadModalVisible = false;
        },
        handleFileSuccess(response,  file, fileList) {
            console.log("success")
            if (response.code !== 200) {
              this.$message.error(response.msg)
              this.$refs.refName.uploadFiles.splice(this.$refs.refName.uploadFiles.indexOf(file), 1);
            }else {
              this.$message.success("")
            }
        },
        handleFileError(err, file, fileList) {
          this.$message.error('!')
          this.$refs.refName.uploadFiles.splice(this.$refs.refName.uploadFiles.indexOf(file), 1);
          this.$refs.refName.clearFiles();
        },
        downloadAttachment(attachment) {
          // 
          console.log('', attachment);
          //  window.open 
          downloadFile(baseURL + '/fault/download/' + attachment.faultFileMidId, attachment.fileName)
        },
        deleteAttachment(row) {
          // 
          console.log('', row);
          this.deleteParams = {
            fileId: row.fileId,
            faultFileMidId: row.faultFileMidId
          }
          // 
          this.$confirm('', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            axios({
              url: baseURL + '/fault/deleteFile',
              method: 'post',
              data: this.deleteParams
            }).then(res => {
              console.log(res)
              if (res.data.code !== 200) {
                this.$message.error(res.data.msg)
              }else {
                this.$message.success('!')
                this.showModal(row.faultId)
              }
            }).catch(err => {
              this.$message.error(err)
            })
          })
        },
        handleFileNameClick(row) {
          // 
          console.log('', row);
          // row.url
          // 
          this.$confirm('', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            axios({
              url: baseURL + '/fault/getFileInfo/' + row.fileId,
              method: 'get'
            }).then(res => {
              console.log(res)
              if (res.data.code !== 200) {
                this.$message.error(res.data.msg)
                this.showModal(row.faultId);
              }
              downloadFile(baseURL + '/fault/download/' + row.faultFileMidId, row.fileName)
            }).catch(err => {
              this.$message.error(err)
            })
          })
        },
    }
}
</script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.rate_wrap {
    display: flex;
    align-items: center;
}

.uploadFile {
  display: flex;
  align-items: center;
  justify-content: center;
}

/*  */
.uploadFile.drag-over {
  border-color: #409eff; /*  */
}

.icon-button {
  border: none; /*  */
  color: #409eff; /*  */
}

/* el-link */
.el-link {
  color: #409eff !important;
}
</style>
  


