<template>
    <div>
        <!--  -->
        <el-breadcrumb class="breadcrumb" separator="/">
            <el-breadcrumb-item :to="{ path: '/technician/faultcarnumber' }"></el-breadcrumb-item>
            <el-breadcrumb-item style="font-size: 36px;"><b>{{ this.$route.query.tag }}</b></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-upload class="upload-demo" :action="`${baseURL}/fault/importcar`" :on-preview="handlePreview"
            :on-remove="handleRemove" :before-remove="beforeRemove" multiple :on-exceed="handleExceed"
            :on-success="handleSuccess" :on-error="handleError">
            <el-button size="small" type="warning" icon="el-icon-star-on"></el-button>
        </el-upload><br>
        <!--  -->
        <!-- <el-form :inline="true" :model="pageparm" class="user-search" ref="searchForm">
            <el-form-item label="" prop="state">
                <el-select v-model="pageparm.state" placeholder="" @change="changeResultSelect">
                    <el-option label="" :value="1"></el-option>
                    <el-option label="" :value="0"></el-option>
                    <el-option label="" :value="2"></el-option>
                    <el-option label="" :value="3"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="result">
                <el-select v-model="pageparm.result" placeholder="" @change="changeResultSelect">
                    <el-option label="" value=""></el-option>
                    <el-option label="" value=""></el-option>
                    <el-option label="" value=""></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="dimension">
                <el-select v-model="pageparm.dimension" placeholder="" @change="changeUpdate">
                    <el-option label="" value="0"></el-option>
                    <el-option label="" value="1"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="carBodyNumber">
                <el-input size="small" v-model="pageparm.carBodyNumber" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-col :span="11">
                    <el-date-picker v-model="timeList" type="daterange" range-separator="" start-placeholder=""
                        end-placeholder="">
                    </el-date-picker>
                </el-col>
            </el-form-item>
            <el-form-item label="" prop="faultDescription">
                <el-input size="small" v-model="pageparm.faultDescription" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="source">
                <el-input size="small" v-model="pageparm.source" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="method">
                <el-input size="small" v-model="pageparm.method" placeholder=""></el-input>
            </el-form-item>
            <el-form-item>
                <el-button size="small" type="primary" icon="el-icon-search" @click="search()"></el-button>
                <el-button size="small" type="primary" icon="el-icon-refresh-right"
                    @click="resetForm('searchForm')"></el-button>
                <el-button size="small" type="warning" icon="el-icon-star-off" @click="exportExcel()"></el-button>
            </el-form-item>
        </el-form> -->

        <!--  -->
        <el-table :data="tableData" style="width: 100%" border v-loading="loading" height="700" :row-style="rowStyle">
            <el-table-column type="index" label="" align="center" width="70" :index="indexMethod" fixed></el-table-column>
            <el-table-column prop="deliveryDate" label="" width="100" align="center"></el-table-column>
            <el-table-column prop="carNumber" label="" width="70" align="center"></el-table-column>
            <el-table-column prop="carBodyNumber" label="" align="center"></el-table-column>
            <el-table-column prop="faultDescription" label="" width="180" align="center"></el-table-column>
            <el-table-column prop="teamsGroups" label="" width="100" align="center"></el-table-column>
            <el-table-column prop="result" label="" width="180" align="center">
                <template slot-scope="scope">
                    {{ scope.row.result == 'null' ? '' : scope.row.result }}
                </template>
            </el-table-column>
            <el-table-column prop="method" label="" width="200" align="center">
                <template slot-scope="scope">
                    {{ scope.row.method == 'null' ? '' : scope.row.method }}
                </template>
            </el-table-column>
            <el-table-column prop="source" label="" align="center">
                <template slot-scope="scope">
                    {{ scope.row.source == 'null' ? '' : scope.row.source }}
                </template>
            </el-table-column>
            <el-table-column prop="handle" label="" align="center">
                <template slot-scope="scope">
                    {{ scope.row.handle == 'null' ? '' : scope.row.handle }}
                </template>
            </el-table-column>
            <el-table-column prop="" label="" width="180" align="center" fixed="right">
                <template slot-scope="scope">
                    <el-button type="danger" icon="el-icon-delete" circle @click="deleteById(scope.row)"></el-button>
                    <el-button type="warning" v-if="scope.row.dimension == 1" icon="el-icon-warning" circle
                        @click="changeDimension(scope.row.id, 0)"></el-button>
                    <el-button v-else="scope.row.dimension == 0" icon="el-icon-warning-outline" circle
                        @click="changeDimension(scope.row.id, 1)"></el-button>
                </template>
            </el-table-column>
        </el-table>

        <!-- <Pagination v-bind:child-msg="pageparm" v-bind:total="total" @callFather="callFather"></Pagination> -->

        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage4"
            :page-sizes="[ 200, 300, 400]" :page-size="100" layout="total, sizes, prev, pager, next, jumper"
            :total="total">
        </el-pagination>

    </div>
</template>
  
<script>
import axios from 'axios'
import Pagination from '../../../components/Pagination.vue'
import { baseURL } from '../../../api/base'
import { carNumberList } from '../.././../common/carNumber'


export default {
    name: 'Fault',
    components: {
        Pagination
    },
    data() {
        return {
            carNumbers: [],
            carNumberList,
            baseURL,
            timeList: [],
            tableData: [],
            result: '',
            pageparm: {
                pageNo: 1,
                pageSize: 200,
            },
            total: 0,
            allList: [],
            loading: true,
            teamsList: [],
            id: 0
        }
    },
    created() {
        this.getFault()
        this.getTeams()
    },
    computed: {
        indexMethod: function () {
            return (this.pageparm.pageNo - 1) * (this.pageparm.pageSize) + 1
        }
    },
    methods: {
        rowStyle({ row }) {
            if (row.dimension == 1) {
                return { 'background': 'antiquewhite' }
            }
        },
        changeUpdate() {
            this.$forceUpdate()
        },
        changeResultSelect() {
            if (this.pageparm.result == '') {
                delete this.pageparm.result
            }
        },
        // 
        resetForm(formName) {
            this.$refs[formName].resetFields();
            this.timeList = []
            delete this.pageparm.startTime
            delete this.pageparm.endTime
            if (this.pageparm.carNumbers) {
                delete this.pageparm.carNumbers
            }
            this.carNumbers = []
        },
        getTeams() {
            axios({
                url: baseURL + '/teams/selectall'
            }).then(res => {
                this.teamsList = res.data.data
                this.loading = false
            }).catch(err => {
                console.log(err);
            })
        },
        search() {
            if (this.carNumbers.length) {
                this.pageparm.carNumbers = this.carNumbers
            }

            this.loading = true
            if (this.timeList.length) {
                this.pageparm.startTime = this.getYMDHMS(new Date(this.timeList[0].toString()));
                this.pageparm.endTime = this.getYMDHMS(new Date(this.timeList[1].toString()))
            }
            this.pageparm.pageNo = 1
            this.pageparm.pageSize = 100000
            this.pageparm.carNumber = this.$route.query.tag
            axios({
                method: 'post',
                url: baseURL + '/fault/screenfault',
                data: this.pageparm
            }).then(res => {

                this.tableData = res.data.data.list
                this.tableData = this.tableData.map(item => {
                    item.deliveryDate = this.getYMDHMS(item.deliveryDate)
                    item.risk = item.risk == '0' ? 0 : parseInt(item.risk)
                    item.code = item.code == 'null' ? '' : item.code
                    item.schedule = item.schedule == 'null' ? '' : item.schedule
                    item.respDepart = item.respDepart == 'null' ? '' : item.respDepart
                    item.notes = item.notes == 'null' ? '' : item.notes
                    return item
                })
                this.total = res.data.data.size
                this.loading = false
                this.$message.success('')
            }).catch(err => {
                this.initPage()
                this.getFault()
                this.$message.error('')
                console.log(err)
            })
        },
        initPage() {
            this.pageparm = {
                pageNo: 1,
                pageSize: 100000,
            }
        },
        getYMDHMS(val) {
            let date = new Date(val)
            let Y = date.getFullYear()
            const M = (date.getMonth() + 1).toString().padStart(2, '0')
            const D = (date.getDate()).toString().padStart(2, '0')
            return Y + "-" + M + "-" + D;
        },
        exportExcel() {
            if (this.pageparm.result == '') {
                delete this.pageparm.result
            }
            if (this.carNumbers.length) {
                this.pageparm.carNumbers = this.carNumbers
            }
            if (this.timeList.length) {
                this.pageparm.startTime = this.getYMDHMS(new Date(this.timeList[0].toString()));
                this.pageparm.endTime = this.getYMDHMS(new Date(this.timeList[1].toString()))
            }
            this.pageparm.carNumber = this.$route.query.tag
            let openUrl = `${baseURL}/fault/export?`;
            for (let obj in this.pageparm) {
                if (this.pageparm[obj]) {
                    openUrl += obj + '=' + this.pageparm[obj] + '&'
                }
            }
            console.log(openUrl)

            axios.get(openUrl).then(res => {
                console.log(openUrl)
                window.location.href = openUrl
            })
        },
        callFather(val) {
            this.pageparm.pageNo = val.pageNo
            this.pageparm.pageSize = val.pageSize
            this.getFault()
        },
        getFault() {
            if (this.carNumbers.length) {
                this.pageparm.carNumbers = this.carNumbers
            }
            axios({
                // url: baseURL + '/fault/screenfaultCarnumber',
                // method: 'post',
                // data: { ...this.pageparm, carNumber: this.$route.query.tag }
                url: baseURL + '/fault/screenfaultCarnumberDetail',
                params: {
                    carNumber: this.$route.query.tag,
                    // pageNo: 1,
                    // pageSize: 3
                    pageNo: this.pageparm.pageNo,
                    pageSize: this.pageparm.pageSize
                }
            }).then(res => {
                this.tableData = res.data.data.list || []
                this.tableData = this.tableData.map(item => {
                    item.deliveryDate = this.getYMDHMS(item.deliveryDate)
                    item.risk = item.risk == '0' ? parseInt(0) : parseInt(item.risk)
                    item.code = item.code == 'null' ? '' : item.code
                    item.schedule = item.schedule == 'null' ? '' : item.schedule
                    item.respDepart = item.respDepart == 'null' ? '' : item.respDepart
                    item.notes = item.notes == 'null' ? '' : item.notes
                    return item
                })
                this.total = res.data.data.size
                this.loading = false
            }).catch(err => {
                console.log(err);
            })
        },
        deleteById(row) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                axios({
                    url: baseURL + '/fault/deletefaultid',
                    params: { id: row.id }
                }).then(res => {
                    this.$message({
                        message: '',
                        type: 'success'
                    });
                    this.getFault()
                }).catch(err => {
                    console.log(err)
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        handleUploadSuccess(res) {
            this.uploadUrlPath = JSON.stringify(res);
            this.loading.close();
        },
        handleBeforeUpload() {
            this.loading = this.$loading({
                lock: true,
                text: "",
                background: "rgba(0, 0, 0, 0.7)",
            });
        },
        handleUploadError() {
            this.$message({
                type: "error",
                message: "",
            });
            this.loading.close();
        },
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePreview(file) {
            console.log(file);
        },
        handleExceed(files, fileList) {
            this.$message.warning(` ${files.length}  ${files.length + fileList.length} `);
        },
        beforeRemove(file, fileList) {
            return this.$confirm(` ${file.name}`);
        },
        handleSuccess() {
            this.getFault()
            this.$message.success("")
        },
        handleError() {
            this.getFault()
            this.$message.error("")
        },
        changeDimension(id, dimension) {
            this.$confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                axios({
                    url: ` ${baseURL}/fault/changedimension`,
                    params: { id, dimension }
                }).then(res => {
                    this.$message.success("")
                    this.search()
                }).catch(err => {
                    this.$message.error('')
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        handleSizeChange(val) {
            this.pageparm.pageSize = val
            this.getFault()
            document.querySelector('.el-main').scrollTo(0,0)
            console.log(` ${val} `);
        },
        handleCurrentChange(val) {
            this.pageparm.pageNo = val
            this.getFault()
            document.querySelector('.el-main').scrollTo(0,0)
            console.log(`: ${val}`);
        }
    }
}
</script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.breadcrumb {
    font-size: 20px;
    margin-bottom: 20px;
}

.rate_wrap {
    display: flex;
    align-items: center;
}
</style>
  


