<template>
    <div>
        <!--  -->
        <el-form :inline="true" :model="pageparm" class="user-search" ref="searchForm">
            <el-form-item label="" prop="result">
                <el-select v-model="pageparm.result" placeholder="" @change="changeResultSelect">
                    <el-option label="" value=""></el-option>
                    <el-option label="" value=""></el-option>
                    <el-option label="" value=""></el-option>
                    <el-option label="" value=""></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="dimension">
                <el-select v-model="pageparm.dimension" placeholder="" @change="changeUpdate">
                    <el-option label="" value="0"></el-option>
                    <el-option label="" value="1"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="material">
                <el-select v-model="pageparm.material" placeholder="">
                    <el-option label="" value=""></el-option>
                    <el-option label="" value=""></el-option>
                    <el-option label="" value=""></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="review">
                <el-select v-model="pageparm.review" placeholder="">
                    <el-option label="" value="0"></el-option>
                    <el-option label="" value="1"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="carNumber">
                <el-select multiple filterable v-model="carNumbers" placeholder="">
                    <el-option v-for="item in carNumberList" :key="item.value" :label="item.value" :value="item.value">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="carBodyNumber">
                <el-input size="small" v-model="pageparm.carBodyNumber" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-col :span="11">
                    <el-date-picker v-model="timeList" type="daterange" range-separator="" start-placeholder=""
                        end-placeholder="">
                    </el-date-picker>
                </el-col>
            </el-form-item>
            <el-form-item label="" prop="faultDescription">
                <el-input size="small" v-model="pageparm.faultDescription" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="source">
                <el-input size="small" v-model="pageparm.source" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="method">
                <el-input size="small" v-model="pageparm.method" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="teamsGroups" @change="changeUpdate">
                <el-input size="small" v-model="pageparm.teamsGroups" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="funClass">
                <el-select v-model="pageparm.funClass" placeholder="" @change="changeUpdate">
                    <el-option label="" value=""></el-option>
                    <el-option label="" value=""></el-option>
                    <el-option label="" value=""></el-option>
                    <el-option label="" value=""></el-option>
                    <el-option label="" value=""></el-option>
                    <el-option label="" value=""></el-option>
                    <el-option label="" value=""></el-option>
                    <el-option label="" value=""></el-option>
                    <el-option label="" value=""></el-option>
                    <el-option label="" value=""></el-option>
                    <el-option label="" value=""></el-option>
                    <el-option label="" value=""></el-option>
                    <el-option label="" value=""></el-option>
                    <el-option label="" value=""></el-option>
                    <el-option label="" value=""></el-option>
                    <el-option label="" value=""></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="handle">
                <el-input size="small" v-model="pageparm.handle" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="risk">
                <el-select multiple filterable v-model="risks" placeholder="" @change="changeUpdate">
                    <el-option label="" :value="0"></el-option>
                    <el-option v-for="item in riskList" :key="item.value" :label="item.label" :value="item.value">
                        <span style="float: left"><el-rate v-model="item.value" :max="item.value" disabled></el-rate></span>
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-button size="small" type="primary" icon="el-icon-search" @click="search()"></el-button>
                <el-button size="small" type="primary" icon="el-icon-refresh-right"
                    @click="resetForm('searchForm')"></el-button>
                <el-button size="small" type="warning" icon="el-icon-star-off" @click="exportExcel()"></el-button>
            </el-form-item>
        </el-form>

        <!--  -->
        <el-table :data="tableData" style="width: 100%" border v-loading="loading" height="600" :row-style="rowStyle">
            <el-table-column type="index" label="" align="center" width="70" :index="indexMethod" fixed></el-table-column>
            <el-table-column prop="deliveryDate" label="" width="100" align="center"></el-table-column>
            <el-table-column prop="carNumber" label="" width="70" align="center"></el-table-column>
            <el-table-column prop="carBodyNumber" label="" align="center"></el-table-column>
            <el-table-column prop="faultDescription" label="" width="180" align="center"></el-table-column>
            <el-table-column prop="teamsGroups" label="" width="100" align="center"></el-table-column>
            <el-table-column prop="result" label="" width="180" align="center">
                <template slot-scope="scope">
                    {{ scope.row.result == 'null' ? '' : scope.row.result }}
                </template>
            </el-table-column>
            <el-table-column prop="method" label="" width="200" align="center">
                <template slot-scope="scope">
                    {{ scope.row.method == 'null' ? '' : scope.row.method }}
                </template>
            </el-table-column>
            <el-table-column prop="material" label="" width="100" align="center"></el-table-column>
            <el-table-column prop="source" label="" align="center">
                <template slot-scope="scope">
                    {{ scope.row.source == 'null' ? '' : scope.row.source }}
                </template>
            </el-table-column>
            <el-table-column prop="funClass" label="" width="130" align="center">
                <template slot-scope="scope">
                    {{ scope.row.funClass == 'null' ? '' : scope.row.funClass }}
                </template>
            </el-table-column>
            <el-table-column prop="risk" label="" width="100" align="center">
                <template slot-scope="scope">
                    <el-rate v-model="scope.row.risk" :max="3" disabled></el-rate>
                </template>
            </el-table-column>
            <el-table-column prop="handle" label="" width="150" align="center">
                <template slot-scope="scope">
                    {{ scope.row.handle == 'null' ? '' : scope.row.handle }}
                </template>
            </el-table-column>
            <el-table-column prop="notes" label="" align="center">
                <template slot-scope="scope">
                    {{ scope.row.notes == 'null' ? '' : scope.row.notes }}
                </template>
            </el-table-column>
            <el-table-column prop="notes" label="" align="center" width="130">
                <template slot-scope="scope">
                    <el-button @click="showModal(scope.row.id)" class="icon-button">
                        <i class="el-icon-view"></i>
                        
                    </el-button>
                </template>
                <el-dialog title="" :visible.sync="dialogVisible" width="40%" :before-close="handleCloseFile"
                    append-to-body>
                    <el-table :data="attachmentList" style="width: 100%">
                        <!-- <el-table-column prop="faultFileMidId" label=""></el-table-column> -->
                        <el-table-column prop="fileName" align="center" label="">
                            <template slot-scope="{ row }">
                                <el-link @click="handleFileNameClick(row)">{{ row.fileName }}</el-link>
                            </template>
                        </el-table-column>
                        <el-table-column prop="fileType" align="center" label=""></el-table-column>
                        <el-table-column label="" align="center">
                            <template slot-scope="{ row }">
                                <el-button type="primary" icon="el-icon-download"
                                    @click="downloadAttachment(row)"></el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-dialog>
            </el-table-column>
        </el-table>

        <Pagination v-bind:child-msg="pageparm" v-bind:total="total" @callFather="callFather"></Pagination>
    </div>
</template>
  
<script>
import axios from 'axios'
import Pagination from '../../../components/Pagination.vue'
import { baseURL } from '../../../api/base'
import { carNumberList } from '../.././../common/carNumber'
import { downloadFile } from '../../../api/FileDownload'

export default {
    name: 'FaultCheck',
    components: {
        Pagination
    },
    data() {
        return {
            carNumbers: [],
            risks: [],
            carNumberList,
            baseURL,
            timeList: [],
            tableData: [],
            editFormVisible: false,
            title: '',
            editForm: {},
            risk: 0,
            result: '',
            rules: {
                deliveryDate: [{ required: true, message: '', trigger: 'blur' }],
                carNumber: [{ required: true, message: '', trigger: 'blur' }],
                carBodyNumber: [{ required: true, message: '', trigger: 'blur' }],
                faultDescription: [{ required: true, message: '', trigger: 'blur' }],
                teamsGroups: [{ required: true, message: '', trigger: 'blur' }],
                funClass: [{ required: true, message: '', trigger: 'blur' }],
                handle: [{ required: true, message: '', trigger: 'blur' }],
                method: [{ required: true, message: '', trigger: 'blur' }],
                source: [{ required: true, message: '', trigger: 'blur' }]
            },
            pageparm: {
                pageNo: 1,
                pageSize: 100000,
                state: 0,
                result: '',
            },
            total: 0,
            allList: [],
            headers: "'Content-Type': 'multipart/form-data'",
            loading: true,
            teamsList: [],
            riskList: [
                {
                    label: "",
                    value: 1
                }, {
                    label: "",
                    value: 2
                }, {
                    label: "",
                    value: 3
                }
            ],

            fileList: [],
            dialogVisible: false,
            attachmentList: [
                // 
                // 
                // { name: '1', type: '', url: '' }
            ],
        }
    },
    created() {
        this.getFault()
        this.getTeams()
    },
    computed: {
        indexMethod: function () {
            return (this.pageparm.pageNo - 1) * (this.pageparm.pageSize) + 1
        }
    },
    methods: {
        changeResultSelect() {
            if (this.pageparm.result == '') {
                delete this.pageparm.result
            }
        },
        rowStyle({ row }) {
            if (row.dimension == 1) {
                return { 'background': 'antiquewhite' }
            }
        },
        changeUpdate() {
            this.$forceUpdate()
        },
        // 
        resetForm(formName) {
            this.$refs[formName].resetFields();
            this.timeList = []
            delete this.pageparm.startTime
            delete this.pageparm.endTime
            if (this.pageparm.carNumbers) {
                delete this.pageparm.carNumbers
            }
            this.carNumbers = []
            if (this.pageparm.risks) {
                delete this.pageparm.risks
            }
            this.risks = []
        },
        getTeams() {
            axios({
                url: baseURL + '/teams/selectall'
            }).then(res => {
                this.teamsList = res.data.data
                this.loading = false
            }).catch(err => {
                console.log(err);
            })
        },
        search() {

            if (this.carNumbers.length) {
                this.pageparm.carNumbers = this.carNumbers
            }

            if (this.risks.length) {
                this.pageparm.risks = this.risks
            }
            this.loading = true
            if (this.timeList.length) {
                this.pageparm.startTime = this.getYMDHMS(new Date(this.timeList[0].toString()));
                this.pageparm.endTime = this.getYMDHMS(new Date(this.timeList[1].toString()))
            }
            this.pageparm.pageNo = 1
            this.pageparm.pageSize = 100000
            axios({
                method: 'post',
                url: baseURL + '/fault/screenfault',
                data: this.pageparm
            }).then(res => {

                this.tableData = res.data.data.list
                this.tableData = this.tableData.map(item => {
                    item.deliveryDate = this.getYMDHMS(item.deliveryDate)
                    item.risk = item.risk == '0' ? 0 : parseInt(item.risk)
                    item.code = item.code == 'null' ? '' : item.code
                    item.schedule = item.schedule == 'null' ? '' : item.schedule
                    item.respDepart = item.respDepart == 'null' ? '' : item.respDepart
                    item.notes = item.notes == 'null' ? '' : item.notes
                    return item
                })
                this.total = res.data.data.size
                this.loading = false
                this.$message.success('')
            }).catch(err => {
                this.initPage()
                this.getFault()
                this.$message.error('')
                console.log(err)
            })
        },
        initPage() {
            this.pageparm = {
                pageNo: 1,
                pageSize: 100000,
                state: 0
            }
        },
        getYMDHMS(val) {
            let date = new Date(val)
            let Y = date.getFullYear()
            const M = (date.getMonth() + 1).toString().padStart(2, '0')
            const D = (date.getDate()).toString().padStart(2, '0')
            return Y + "-" + M + "-" + D;
        },
        exportExcel() {
            if (this.pageparm.result == '') {
                delete this.pageparm.result
            }
            if (this.carNumbers.length) {
                this.pageparm.carNumbers = this.carNumbers
            }
            if (this.timeList.length) {
                this.pageparm.startTime = this.getYMDHMS(new Date(this.timeList[0].toString()));
                this.pageparm.endTime = this.getYMDHMS(new Date(this.timeList[1].toString()))
            }
            if (this.risks.length) {
                this.pageparm.risks = this.risks
            }

            let openUrl = `${baseURL}/fault/export?`;

            for (let obj in this.pageparm) {
                if (this.pageparm[obj]) {
                    openUrl += obj + '=' + this.pageparm[obj] + '&'
                }
            }

            console.log(openUrl)

            axios.get(openUrl).then(res => {
                console.log(openUrl)
                window.location.href = openUrl
            })

        },
        callFather(val) {
            this.pageparm.pageNo = val.pageNo
            this.pageparm.pageSize = val.pageSize
            this.getFault()
        },
        getFault() {
            if (this.carNumbers.length) {
                this.pageparm.carNumbers = this.carNumbers
            }
            axios({
                url: baseURL + '/fault/screenfault',
                method: 'post',
                data: {
                    result: '',
                    state: '0',
                    pageNo: this.pageparm.pageNo,
                    pageSize: this.pageparm.pageSize
                }

            }).then(res => {
                this.tableData = res.data.data.list || []
                this.tableData = this.tableData.map(item => {
                    item.deliveryDate = this.getYMDHMS(item.deliveryDate)
                    item.risk = item.risk == '0' ? parseInt(0) : parseInt(item.risk)
                    item.code = item.code == 'null' ? '' : item.code
                    item.schedule = item.schedule == 'null' ? '' : item.schedule
                    item.respDepart = item.respDepart == 'null' ? '' : item.respDepart
                    item.notes = item.notes == 'null' ? '' : item.notes
                    return item
                })
                this.total = res.data.data.size
                this.loading = false
            }).catch(err => {
                console.log(err);
            })
        },
        deleteById(row) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                axios({
                    url: baseURL + '/fault/deletefaultid',
                    params: { id: row.id }
                }).then(res => {
                    this.$message({
                        message: '',
                        type: 'success'
                    });
                    this.getFault()
                }).catch(err => {
                    console.log(err)
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        handleEdit(index, row) {
            this.editFormVisible = true
            if (row != undefined && row != 'undefined') {
                this.title = ''
                this.editForm = row
                if (this.editForm.result != '' & this.editForm.result != '') {
                    this.result = ''
                } else if (this.editForm.result == '') {
                    this.result = ''
                } else if (this.editForm.result == "") {
                    this.result = ''
                }
            } else {
                this.title = ''
                this.editForm = {}
                this.result = ''
            }
        },
        changeResult() {
            this.editForm.result = ''
        },
        handelFault() {
            this.$refs['editForm'].validate((valid) => {
                if (valid) {
                    if (this.result != '') {
                        this.editForm.result = this.result
                    }
                    this.editForm.risk = this.risk
                    if (this.title == '') {
                        this.editForm.dimension = "0"
                        axios({
                            url: baseURL + '/fault/insertfault',
                            method: 'post',
                            data: this.editForm
                        }).then(res => {
                            this.$message({
                                message: this.title + '',
                                type: 'success'
                            });
                            this.editFormVisible = false
                            this.editForm = {}
                            this.initPage()
                            this.getFault()
                        }).catch(err => {
                            console.log(err)
                            this.$message({
                                message: this.title + '',
                                type: 'error'
                            });
                            this.editFormVisible = false
                            this.editForm = {}
                            this.initPage()
                            this.getFault()
                        })
                    } else {
                        for (let obj in this.editForm) {
                            if (!this.editForm[obj] & obj != 'risk') {
                                this.editForm[obj] = 'null'
                            }
                        }
                        // console.log(this.editForm)
                        axios({
                            url: baseURL + '/fault/updatefaultid',
                            method: 'post',
                            data: this.editForm,
                        }).then(res => {
                            this.editFormVisible = false
                            this.editForm = {}
                            // this.initPage()
                            // this.getFault()
                            this.$message.success('')
                        }).catch(err => {
                            console.log(err)
                            this.$message.error("")
                        })
                    }
                } else {
                    console.log('');
                    return false;
                }
            });
            // if (this.result != '') {
            //     this.editForm.result = this.result
            // }
            // if (this.title == '') {
            //     axios({
            //         url: baseURL + '/fault/insertfault',
            //         method: 'post',
            //         data: this.editForm
            //     }).then(res => {
            //         this.$message({
            //             message: this.title + '',
            //             type: 'success'
            //         });
            //         this.editFormVisible = false
            //         this.editForm = {}
            //         this.initPage()
            //         this.getFault()
            //     }).catch(err => {
            //         console.log(err)
            //         this.$message({
            //             message: this.title + '',
            //             type: 'error'
            //         });
            //         this.editFormVisible = false
            //         this.editForm = {}
            //         this.initPage()
            //         this.getFault()
            //     })
            // } else {
            //     axios({
            //         url: baseURL + '/fault/updatefaultid',
            //         method: 'post',
            //         data: this.editForm,
            //     }).then(res => {
            //         this.editFormVisible = false
            //         this.editForm = {}
            //         this.initPage()
            //         this.getFault()
            //     }).catch(err => {
            //         console.log(err)
            //     })
            // }

        },
        closeDialog() {
            this.editFormVisible = false
            this.editForm = {}
            this.initPage()
            this.getFault()
        },
        handleUploadSuccess(res) {
            this.uploadUrlPath = JSON.stringify(res);
            this.loading.close();
        },
        handleBeforeUpload() {
            this.loading = this.$loading({
                lock: true,
                text: "",
                background: "rgba(0, 0, 0, 0.7)",
            });
        },
        handleUploadError() {
            this.$message({
                type: "error",
                message: "",
            });
            this.loading.close();
        },

        handleRemove(file, fileList) {
            console.log(file, fileList);

        },
        handlePreview(file) {
            console.log(file);
        },
        handleExceed(files, fileList) {
            this.$message.warning(` ${files.length}  ${files.length + fileList.length} `);
        },
        beforeRemove(file, fileList) {
            return this.$confirm(` ${file.name}`);
        },
        handleSuccess() {
            this.getFault()
            this.$message.success("")
        },
        handleError() {
            this.getFault()
            this.$message.error("")

        },
        resetRisk() {
            this.risk = 0
        },
        changState(id, state) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                axios({
                    url: ` ${baseURL}/fault/changestate`,
                    params: { id, state: 1 }
                }).then(res => {
                    this.$message.success("")
                    this.search()
                }).catch(err => {
                    this.$message.error('')
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        changeDimension(id, dimension) {
            this.$confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                axios({
                    url: ` ${baseURL}/fault/changedimension`,
                    params: { id, dimension }
                }).then(res => {
                    this.$message.success("")
                    this.search()
                }).catch(err => {
                    this.$message.error('')
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        showModal(id) {
            // 
            axios({
                url: baseURL + '/fault/fileList/' + id,
                method: 'get'
            }).then(res => {
                this.dialogVisible = true;
                this.attachmentList = res.data.data || []
            })
        },
        handleCloseFile(done) {
            // 
            done(); //  done() 
        },
        downloadAttachment(attachment) {
            // 
            console.log('', attachment);
            //  window.open 
            downloadFile(baseURL + '/fault/download/' + attachment.faultFileMidId, attachment.fileName)
        },
    }
}
</script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.rate_wrap {
    display: flex;
    align-items: center;
}

.uploadFile {
    display: flex;
    align-items: center;
    justify-content: center;
}

/*  */
.uploadFile.drag-over {
    border-color: #409eff;
    /*  */
}

.icon-button {
    border: none;
    /*  */
    color: #409eff;
    /*  */
}

/* el-link */
.el-link {
    color: #409eff !important;
}
</style>
  


