<script>
import axios from 'axios'
import Pagination from '../../../components/Pagination.vue'
import { baseURL } from '../../../api/base'
import { carNumberList } from '../.././../common/carNumber'
import { downloadFile } from '../../../api/FileDownload'


export default {
    name: 'FaultDecorate',
    components: {
        Pagination
    },
    data() {
        return {
            carNumbers: [],
            risks: [],
            carNumberList,
            baseURL,
            timeList: [],
            tableData: [],
            editFormVisible: false,
            dialogTrackVisible: false,
            title: '',
            editForm: {
                code: '',
                schedule: '',
                respDepart: '',
                review: '0',
                review: '',

            },
            editTrackForm: {},
            risk: 0,
            result: '',
            rules: {
                deliveryDate: [{ required: true, message: '', trigger: 'blur' }],
                carNumber: [{ required: true, message: '', trigger: 'blur' }],
                carBodyNumber: [{ required: true, message: '', trigger: 'blur' }],
                faultDescription: [{ required: true, message: '', trigger: 'blur' }],
                teamsGroups: [{ required: true, message: '', trigger: 'blur' }],
                funClass: [{ required: true, message: '', trigger: 'blur' }],
                handle: [{ required: true, message: '', trigger: 'blur' }],
                method: [{ required: true, message: '', trigger: 'blur' }],
                source: [{ required: true, message: '', trigger: 'blur' }],
                material: [{ required: true, message: '', trigger: 'blur' }]
            },
            trackRules: {
                trackPerson: [{ required: true, message: '', trigger: 'blur' }],
                trackRequire: [{ required: true, message: '', trigger: 'blur' }],
                feedback: [{ required: true, message: '', trigger: 'blur' }],
            },
            pageparm: {
                pageNo: 1,
                pageSize: 10000,
                result: '',
                state: '4',
                // dimension: '',
                // material: '',
                // carBodyNumber: '',
                // faultDescription: '',
                // schedule: '',
                // source: '',
                // method: '',
                // teamsGroups: '',
                // funClass: '',
                // handle: ''
            },
            total: 0,
            allList: [],
            loading: true,
            teamsList: [],
            riskList: [
                {
                    label: "",
                    value: 1
                }, {
                    label: "",
                    value: 2
                }, {
                    label: "",
                    value: 3
                }
            ],
            id: 0,
            uploadModalVisible: false,
            fileList: [],
            dialogVisible: false,
            attachmentList: [
                // 
                // 
                // { name: '1', type: '', url: '' }
            ],
            previewDialogVisible: false,
            previewFile: '',
            fault: {},
            deleteParams: {}
        }
    },
    created() {
        this.getFault()
        this.getTeams()
    },
    computed: {
        indexMethod: function () {
            return (this.pageparm.pageNo - 1) * (this.pageparm.pageSize) + 1
        },
    },
    methods: {
        changeInput(e) {
            this.$forceUpdate();
        },
        rowStyle({ row }) {
            if (row.dimension == 1) {
                return { 'background': 'antiquewhite' }
            }
            if (row.review == 1) {
                return { 'background': 'rgb(159 199 159)' }
            }
        },
        changeUpdate() {
            this.$forceUpdate()
        },
        changeResultSelect() {
            if (this.pageparm.result == '') {
                delete this.pageparm.result
            }

        },
        // 
        resetForm(formName) {
            this.$refs[formName].resetFields();
            this.timeList = []
            delete this.pageparm.startTime
            delete this.pageparm.endTime
            if (this.pageparm.carNumbers) {
                delete this.pageparm.carNumbers
            }
            this.carNumbers = []
            if (this.pageparm.risks) {
                delete this.pageparm.risks
            }
            this.risks = []
        },
        getTeams() {
            axios({
                url: baseURL + '/teams/selectall'
            }).then(res => {
                this.teamsList = res.data.data
                this.loading = false
            }).catch(err => {
                console.log(err);
            })
        },
        search() {
            if (this.carNumbers.length) {
                this.pageparm.carNumbers = this.carNumbers
            }
            if (this.risks.length) {
                this.pageparm.risks = this.risks
            }
            this.loading = true
            if (this.timeList.length) {
                this.pageparm.startTime = this.getYMDHMS(new Date(this.timeList[0].toString()));
                this.pageparm.endTime = this.getYMDHMS(new Date(this.timeList[1].toString()))
            }
            this.pageparm.pageNo = 1
            // this.pageparm.pageSize = 100
            axios({
                // get
                // url: baseURL + '/fault/screenfault',
                // params: {
                //     // record: this.pageparm,
                //     ...this.pageparm,
                //     state: '0',
                //     pageNo: this.pageparm.pageNo,
                //     pageSize: this.pageparm.pageSize
                // }

                // post
                method: 'post',
                url: baseURL + '/fault/screenfault',
                // data: this.pageparm
                data: {
                    ...this.pageparm,
                    pageNo: this.pageparm.pageNo,
                    pageSize: this.pageparm.pageSize
                }
            }).then(res => {
                this.tableData = res.data.data.list
                this.tableData = this.tableData.map(item => {
                    item.deliveryDate = this.getYMDHMS(item.deliveryDate)
                    item.risk = item.risk == '0' ? 0 : parseInt(item.risk)
                    item.code = item.code == 'null' ? '' : item.code
                    item.schedule = item.schedule == 'null' ? '' : item.schedule
                    item.respDepart = item.respDepart == 'null' ? '' : item.respDepart
                    item.notes = item.notes == 'null' ? '' : item.notes
                    return item
                })
                this.total = res.data.data.size
                this.loading = false
                this.$message.success('')
            }).catch(err => {
                this.initPage()
                this.getFault()
                this.$message.error('')
            })
        },
        searchCurrent() {
            if (this.carNumbers.length) {
                this.pageparm.carNumbers = this.carNumbers
            }
            if (this.risks.length) {
                this.pageparm.risks = this.risks
            }
            this.loading = true
            if (this.timeList.length) {
                this.pageparm.startTime = this.getYMDHMS(new Date(this.timeList[0].toString()));
                this.pageparm.endTime = this.getYMDHMS(new Date(this.timeList[1].toString()))
            }
            this.pageparm.pageNo = 1
            this.pageparm.pageSize = 10000
            axios({
                // get
                // url: baseURL + '/fault/screenfault',
                // params: {
                //     // record: this.pageparm,
                //     ...this.pageparm,
                //     state: '0',
                //     pageNo: this.pageparm.pageNo,
                //     pageSize: this.pageparm.pageSize
                // }

                // post
                method: 'post',
                url: baseURL + '/fault/screenfault',
                data: this.pageparm
                // data: {
                //     ...this.pageparm,
                //     pageNo: this.pageparm.pageNo,
                //     pageSize: this.pageparm.pageSize
                // }
            }).then(res => {
                this.tableData = res.data.data.list
                this.tableData = this.tableData.map(item => {
                    item.deliveryDate = this.getYMDHMS(item.deliveryDate)
                    item.risk = item.risk == '0' ? 0 : parseInt(item.risk)
                    item.code = item.code == 'null' ? '' : item.code
                    item.schedule = item.schedule == 'null' ? '' : item.schedule
                    item.respDepart = item.respDepart == 'null' ? '' : item.respDepart
                    item.notes = item.notes == 'null' ? '' : item.notes
                    return item
                })
                this.total = res.data.data.size
                this.loading = false
                this.$message.success('')
            }).catch(err => {
                this.initPage()
                this.getFault()
                this.$message.error('')
            })
        },
        initPage() {
            this.pageparm = {
                pageNo: 1,
                pageSize: 10000,
                state: 0,
                result: ''
            }
        },
        getYMDHMS(val) {
            let date = new Date(val)
            let Y = date.getFullYear()
            const M = (date.getMonth() + 1).toString().padStart(2, '0')
            const D = (date.getDate()).toString().padStart(2, '0')
            return Y + "-" + M + "-" + D;
        },
        exportExcel() {
            if (this.pageparm.result == '') {
                delete this.pageparm.result
            }
            if (this.carNumbers.length) {
                this.pageparm.carNumbers = this.carNumbers
            }
            if (this.timeList.length) {
                this.pageparm.startTime = this.getYMDHMS(new Date(this.timeList[0].toString()));
                this.pageparm.endTime = this.getYMDHMS(new Date(this.timeList[1].toString()))
            }

            if (this.risks.length) {
                this.pageparm.risks = this.risks
            }

            let openUrl = `${baseURL}/fault/export?`;

            // let carNumbersList = `carNumbers=`
            // if (this.pageparm['carNumbers'].length) {
            //     for (let i = 0; i < this.pageparm['carNumbers'].length; i++) {
            //         carNumbersList += this.pageparm['carNumbers'][i] + ','
            //     }
            // }

            for (let obj in this.pageparm) {
                if (this.pageparm[obj]) {
                    openUrl += obj + '=' + this.pageparm[obj] + '&'
                }
            }

            console.log(openUrl)

            axios.get(openUrl).then(res => {
                console.log(openUrl)
                window.location.href = openUrl
            })


            // axios({
            //     url: `${baseURL}/fault/export`,
            //     // method: 'POST',
            //     params: this.pageparm,
            // }).then(res => {

            //     // let openUrl = `${baseURL}/fault/export?`;
            //     // let carNumbersList = `carNumbers=`
            //     // for (let obj in this.pageparm) {
            //     //     if (this.pageparm[obj]) {
            //     //         openUrl += obj + '=' + this.pageparm[obj] + '&'
            //     //     }
            //     //     let carNumbersList = `carNumbers=`
            //     //     if (this.pageparm['carNumbers'].length) {
            //     //         for (let i = 0; i < this.pageparm['carNumbers'].length; i++) {
            //     //             carNumbersList += this.pageparm['carNumbers'][i] + ','
            //     //         }
            //     //         console.log(carNumbersList)
            //     //     }
            //     // }

            //     // console.log(openUrl, carNumbersList)
            //     // window.location.href = openUrl

            // let openUrl = `${baseURL}/fault/export?`;
            // let carNumbersList = `carNumbers=`

            // for (let obj in this.pageparm) {
            //     if (this.pageparm[obj]) {
            //         openUrl += obj + '=' + this.pageparm[obj] + '&'
            //     }

            //     if (this.pageparm['carNumbers'].length) {
            //         for (let i = 0; i < this.pageparm['carNumbers'].length; i++) {
            //             carNumbersList += this.pageparm['carNumbers'][i] + ','
            //         }
            //         console.log(carNumbersList)
            //         openUrl += carNumbersList
            //     }
            // }
            // console.log(openUrl)
            //     // window.location.href = openUrl

            // }).catch(err => {
            //     this.$message.error('')
            // })
        },
        callFather(val) {

            this.pageparm.pageNo = val.pageNo
            this.pageparm.pageSize = val.pageSize
            this.searchCurrent()
            document.querySelector('.el-main').scrollTo(0, 0)
        },
        getFault() {
            if (this.carNumbers.length) {
                this.pageparm.carNumbers = this.carNumbers
            }
            axios({

                // get
                // url: baseURL + '/fault/screenfault',
                // params: {
                //     // record: this.pageparm,
                //     result: '',
                //     state: '0',
                //     pageNo: this.pageparm.pageNo,
                //     pageSize: this.pageparm.pageSize
                // }

                // post
                method: 'post',
                url: baseURL + '/fault/screenfault',
                data: {
                    result: '',
                    state: '4',
                    pageNo: this.pageparm.pageNo,
                    pageSize: this.pageparm.pageSize
                }
            }).then(res => {
                this.tableData = res.data.data.list || []
                this.tableData = this.tableData.map(item => {
                    item.deliveryDate = this.getYMDHMS(item.deliveryDate)
                    item.risk = item.risk == '0' ? parseInt(0) : parseInt(item.risk)
                    item.code = item.code == 'null' ? '' : item.code
                    item.schedule = item.schedule == 'null' ? '' : item.schedule
                    item.respDepart = item.respDepart == 'null' ? '' : item.respDepart
                    item.notes = item.notes == 'null' ? '' : item.notes
                    return item
                })
                this.total = res.data.data.size
                this.loading = false
            }).catch(err => {
                console.log(err);
            })
        },
        deleteById(row) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                axios({
                    url: baseURL + '/fault/deletefaultid',
                    params: { id: row.id }
                }).then(res => {
                    this.$message({
                        message: '',
                        type: 'success'
                    });
                    this.search()
                }).catch(err => {
                    console.log(err)
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        handleEdit(index, row) {
            console.log(row)
            this.editFormVisible = true
            if (row != undefined && row != 'undefined') {
                this.title = ''
                this.editForm = row
                if (this.editForm.result != '' & this.editForm.result != '') {
                    this.result = ''
                } else if (this.editForm.result == '') {
                    this.result = ''
                } else if (this.editForm.result == "") {
                    this.result = ''
                }
                this.risk = row.risk
            } else {
                this.title = ''
                this.editForm = {
                    review: '0',
                    code: '',
                    schedule: '',
                    respDepart: '',
                    notes: '',
                    // material: ''
                }
                this.result = ''
            }
        },
        changeResult() {
            this.editForm.result = ''
        },
        handelFault() {
            this.$refs['editForm'].validate((valid) => {
                if (valid) {
                    if (this.result != '') {
                        this.editForm.result = this.result
                    }
                    this.editForm.risk = this.risk
                    if (this.title == '') {
                        //  
                        if (!this.editForm.result) {
                            this.$message.error("")
                        } else {
                            // 
                            this.editForm.dimension = "0"
                            axios({
                                url: baseURL + '/fault/insertfault',
                                method: 'post',
                                data: this.editForm
                            }).then(res => {
                                this.$message({
                                    message: this.title + '',
                                    type: 'success'
                                });
                                this.editFormVisible = false
                                this.editForm = {}
                                this.risk = 0
                                this.initPage()
                                this.search()
                            }).catch(err => {
                                console.log(err)
                                this.$message({
                                    message: this.title + '',
                                    type: 'error'
                                });
                                this.editFormVisible = false
                                this.editForm = {}
                                this.risk = 0
                                this.initPage()
                                this.search()
                            })
                        }


                    } else {
                        for (let obj in this.editForm) {
                            if (!this.editForm[obj] & obj != 'risk') {
                                this.editForm[obj] = 'null'
                            }
                        }
                        axios({
                            url: baseURL + '/fault/updatefaultid',
                            method: 'post',
                            data: this.editForm,
                        }).then(res => {
                            this.editFormVisible = false
                            this.editForm = {}
                            this.risk = 0
                            this.$message.success('')
                        }).catch(err => {
                            console.log(err)
                            this.$message.error("")
                        })
                    }
                } else {
                    console.log('');
                    return false;
                }
            });
        },
        closeDialog() {
            this.editFormVisible = false
            this.editForm = {}
            this.risk = 0
        },
        handleUploadSuccess(res) {
            this.uploadUrlPath = JSON.stringify(res);
            this.loading.close();
        },
        handleBeforeUpload() {
            this.loading = this.$loading({
                lock: true,
                text: "",
                background: "rgba(0, 0, 0, 0.7)",
            });
        },
        handleUploadError() {
            this.$message({
                type: "error",
                message: "",
            });
            this.loading.close();
        },
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePreview(file) {
            console.log(file);
        },
        handleExceed(files, fileList) {
            this.$message.warning(` ${files.length}  ${files.length + fileList.length} `);
        },
        beforeRemove(file, fileList) {
            return this.$confirm(` ${file.name}`);
        },
        handleSuccess() {
            this.getFault()
            this.$message.success("")
        },
        handleError() {
            this.getFault()
            this.$message.error("")

        },
        resetRisk() {
            this.risk = 0
        },
        changState(id, state) {
            // let test = state == 0 ? '' ? ''
            this.$confirm(`, ?`,'', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                axios({
                    url: ` ${baseURL}/fault/changestate`,
                    params: { id, state }
                }).then(res => {
                    this.$message.success("")
                    this.search()
                }).catch(err => {
                    this.$message.error('')
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        changeTrack() {
            this.$refs['editTrackForm'].validate((valid) => {
                if (valid) {
                    this.$confirm(', ?', '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                    }).then(() => {
                        axios({
                            url: ` ${baseURL}/fault/changestate`,
                            params: {
                                id: this.id,
                                state: 2,
                                trackPerson: this.editTrackForm.trackPerson,
                                trackRequire: this.editTrackForm.trackRequire,
                                feedback: ''
                            }
                        }).then(res => {
                            this.$message.success("")
                            this.search()
                            this.dialogTrackVisible = false
                            this.editTrackForm = {}
                        }).catch(err => {
                            this.$message.error('')
                            this.editTrackForm = {}
                        })
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: ''
                        });
                    });
                } else {
                    console.log('');
                    return false;
                }
            });
        },
        changeDimension(id, dimension) {
            this.$confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                axios({
                    url: ` ${baseURL}/fault/changedimension`,
                    params: { id, dimension }
                }).then(res => {
                    this.$message.success("")
                    this.search()
                }).catch(err => {
                    this.$message.error('')
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        handleTrack(id) {
            this.dialogTrackVisible = true
            this.id = id
        },
        handleClose() {
            this.editTrackForm = {}
            this.dialogTrackVisible = false
        },
        handleMethod() {
            if (this.title == '') {
                this.editForm.handle = ''
            }
        },
        // 
        showUploadModal(row) {
            this.fault.id = row.id;
            this.uploadModalVisible = true;
        },
        // 
        beforeClose(done) {
            // 
            done();
        },
        // 
        upload() {
            // 
            this.$refs.refName.clearFiles();
            this.uploadModalVisible = false;
        },
        handleFileSuccess(response, file, fileList) {
            if (response.code !== 200) {
                this.$message.error(response.msg)
                this.$refs.refName.uploadFiles.splice(this.$refs.refName.uploadFiles.indexOf(file), 1);
            } else {
                this.$message.success("")
            }
        },
        handleFileError(err, file, fileList) {
            this.$message.error('!')
            this.$refs.refName.uploadFiles.splice(this.$refs.refName.uploadFiles.indexOf(file), 1);
            this.$refs.refName.clearFiles();
        },
        showModal(id) {
            // 
            axios({
                url: baseURL + '/fault/fileList/' + id,
                method: 'get'
            }).then(res => {
                this.dialogVisible = true;
                this.attachmentList = res.data.data || []
            })
        },
        handleCloseFile(done) {
            // 
            done(); //  done() 
        },
        downloadAttachment(attachment) {
            // 
            console.log('', attachment);
            //  window.open 
            downloadFile(baseURL + '/fault/download/' + attachment.faultFileMidId, attachment.fileName)
        },
        deleteAttachment(row) {
            // 
            console.log('', row);
            this.deleteParams = {
                fileId: row.fileId,
                faultFileMidId: row.faultFileMidId
            }
            // 
            this.$confirm('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                axios({
                    url: baseURL + '/fault/deleteFile',
                    method: 'post',
                    data: this.deleteParams
                }).then(res => {
                    console.log(res)
                    if (res.data.code !== 200) {
                        this.$message.error(res.data.msg)
                    } else {
                        this.$message.success('!')
                        this.showModal(row.faultId)
                    }
                }).catch(err => {
                    this.$message.error(err)
                })
            })
        },
        handleFileNameClick(row) {
            // 
            console.log('', row);
            // row.url
            // 
            this.$confirm('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                axios({
                    url: baseURL + '/fault/getFileInfo/' + row.fileId,
                    method: 'get'
                }).then(res => {
                    console.log(res)
                    if (res.data.code !== 200) {
                        this.$message.error(res.data.msg)
                        this.showModal(row.faultId);
                    }
                    downloadFile(baseURL + '/fault/download/' + row.faultFileMidId, row.fileName)
                }).catch(err => {
                    this.$message.error(err)
                })
            })
        },

        handleSizeChange(val) {
            this.pageparm.pageSize = val
            this.getFault()
            document.querySelector('.el-main').scrollTo(0, 0)
            console.log(` ${val} `);
        },
        handleCurrentChange(val) {
            this.pageparm.pageNo = val
            this.getFault()
            document.querySelector('.el-main').scrollTo(0, 0)
            console.log(`: ${val}`);
        },
        goTeam() {
            this.$router.push({ path: '/teams/index' })
        }

    }
}
</script>

  <!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.rate_wrap {
    display: flex;
    align-items: center;
}

.uploadFile {
    display: flex;
    align-items: center;
    justify-content: center;
}

/*  */
.uploadFile.drag-over {
    border-color: #409eff;
    /*  */
}

.icon-button {
    border: none;
    /*  */
    color: #409eff;
    /*  */
}

/* el-link */
.el-link {
    color: #409eff !important;
}
</style>



