            </el-table-column>
            <el-table-column prop="risk" label="" width="140" align="center">
                <template slot-scope="scope">
                    <el-rate v-model="scope.row.risk" :max="3" disabled></el-rate>
                </template>
            </el-table-column>
            <el-table-column prop="handle" label="" width="150" align="center">
                <template slot-scope="scope">
                    {{ scope.row.handle == 'null' ? '' : scope.row.handle }}
                </template>
            </el-table-column>
            <el-table-column prop="notes" label="" align="center">
                <template slot-scope="scope">
                    {{ scope.row.notes == 'null' ? '' : scope.row.notes }}
                </template>
            </el-table-column>
            <el-table-column prop="" label="" width="180" align="center" fixed="right">
                <template slot-scope="scope">

                    <el-button type="primary" icon="el-icon-edit" circle
                        @click="handleEdit(scope.$index, scope.row)"></el-button>
                    <el-button type="danger" icon="el-icon-delete" circle @click="deleteById(scope.row)"></el-button>
                    <el-button type="success" icon="el-icon-top" circle
                        @click="changState(scope.row.id, scope.row.state)"></el-button>

                    <el-button type="warning" v-if="scope.row.dimension == 1" icon="el-icon-warning" circle
                        @click="changeDimension(scope.row.id, 0)"></el-button>
                    <el-button v-else="scope.row.dimension == 0" icon="el-icon-warning-outline" circle
                        @click="changeDimension(scope.row.id, 1)"></el-button>

                </template>
            </el-table-column>
        </el-table>

        <Pagination v-bind:child-msg="pageparm" v-bind:total="total" @callFather="callFather"></Pagination>

        <!--  -->
        <el-dialog :title="title" :visible.sync="editFormVisible" width="50%" @click="closeDialog('editForm')">
            <el-form label-width="120px" :model="editForm" :rules="rules" ref="editForm">
                <el-form-item label="" prop="deliveryDate">
                    <el-date-picker value-format="yyyy-MM-dd" v-model="editForm.deliveryDate" type="date"
                        placeholder="">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="" prop="carNumber">
                    <el-input v-model="editForm.carNumber" auto-complete="off" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="carBodyNumber">
                    <el-input v-model="editForm.carBodyNumber" :min="1" label="" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="faultDescription">
                    <el-input type="textarea" v-model="editForm.faultDescription" label="" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="teamsGroups">
                    <el-select v-model="editForm.teamsGroups" placeholder="">
                        <el-option v-for="item in teamsList" :key="item.id" :label="item.name" :value="item.name">
                        </el-option>
                    </el-select>
                    <el-link type="primary" href="http://localhost:8080/#/teams/index" :underline="false"></el-link>
                </el-form-item>
                <el-form-item label="" prop="result">
                    <label slot="label"><span style="color:red">*</span>&nbsp;</label>
                    <el-radio-group v-model="result" @change="changeResult">
                        <el-radio class="radio" label=""></el-radio>
                        <el-radio class="radio" label=""></el-radio>
                        <el-radio class="radio" label="">()</el-radio>
                    </el-radio-group>
                    <el-input v-if="result == ''" v-model="editForm.result" label="" placeholder=""
                        size="mini"></el-input>
                </el-form-item>
                <el-form-item label="" prop="handle">
                    <el-select v-model="editForm.handle" placeholder="">
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="funClass">
                    <el-select v-model="editForm.funClass" placeholder="">
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="method">
                    <el-input type="textarea" size="small" v-model="editForm.method" auto-complete="off"
                        placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="code">
                    <el-input size="small" v-model="editForm.code" auto-complete="off" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="schedule">
                    <el-input size="small" v-model="editForm.schedule" auto-complete="off"
                        placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="respDepart">
                    <el-input v-model="editForm.respDepart" label="" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="source">
                    <el-input v-model="editForm.source" label="" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="risk">
                    <div class="rate_wrap">
                        <el-rate v-model="risk" :max="3"></el-rate>
                        <el-button size="mini" icon="el-icon-refresh-right" circle @click="resetRisk"></el-button>
                    </div>
                </el-form-item>
                <el-form-item label="" prop="node">
                    <el-input size="small" v-model="editForm.notes" auto-complete="off" placeholder=""></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button size="small" @click="closeDialog('editForm')"></el-button>
                <el-button size="small" type="primary" class="title" @click="handelFault('editForm')"></el-button>
            </div>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="dialogTrackVisible" width="45%" :before-close="handleClose">
            <el-form label-width="120px" :model="editTrackForm" ref="editTrackForm" :rules="trackRules">
                <el-form-item label="" prop="trackPerson">
                    <el-input v-model="editTrackForm.trackPerson" auto-complete="off" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="trackRequire">
                    <el-input type="textarea" v-model="editTrackForm.trackRequire" auto-complete="off"
                        placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="feedback">
                    <el-input type="textarea" v-model="editTrackForm.feedback" auto-complete="off"
                        placeholder=""></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogTrackVisible = false"> </el-button>
                <el-button type="primary" @click="changeTrack"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>
  
<script>
import axios from 'axios'
import Pagination from '../../../components/Pagination.vue'
import { baseURL } from '../../../api/base'
import { carNumberList } from '../.././../common/carNumber'


export default {
    name: 'Fault',
    components: {
        Pagination
    },
    data() {
        return {
            carNumbers: [],
            carNumberList,
            baseURL,
            timeList: [],
            tableData: [],
            editFormVisible: false,
            dialogTrackVisible: false,
            title: '',
            editForm: {},
            editTrackForm: {},
            risk: 0,
            result: '',
            rules: {
                deliveryDate: [{ required: true, message: '', trigger: 'blur' }],
                carNumber: [{ required: true, message: '', trigger: 'blur' }],
                carBodyNumber: [{ required: true, message: '', trigger: 'blur' }],
                faultDescription: [{ required: true, message: '', trigger: 'blur' }],
                teamsGroups: [{ required: true, message: '', trigger: 'blur' }],
                funClass: [{ required: true, message: '', trigger: 'blur' }],
                handle: [{ required: true, message: '', trigger: 'blur' }],
                method: [{ required: true, message: '', trigger: 'blur' }],
                source: [{ required: true, message: '', trigger: 'blur' }]
            },
            trackRules: {
                trackPerson: [{ required: true, message: '', trigger: 'blur' }],
                trackRequire: [{ required: true, message: '', trigger: 'blur' }],
                feedback: [{ required: true, message: '', trigger: 'blur' }],
            },
            pageparm: {
                pageNo: 1,
                pageSize: 100000,
                result: '',
                state: 0,
            },
            total: 0,
            allList: [],
            headers: "'Content-Type': 'multipart/form-data'",
            loading: true,
            teamsList: [],
            riskList: [{
                label: "",
                value: 1
            }, {
                label: "",
                value: 2
            }, {
                label: "",
                value: 3
            },],
            id: 0
        }
    },
    created() {
        this.getFault()
        this.getTeams()
    },
    computed: {
        indexMethod: function () {
            return (this.pageparm.pageNo - 1) * (this.pageparm.pageSize) + 1
        }
    },
    methods: {
        rowStyle({ row }) {
            if (row.dimension == 1) {
                return { 'background': 'antiquewhite' }
            }
        },
        changeUpdate() {
            this.$forceUpdate()
        },
        changeResultSelect() {
            if (this.pageparm.result == '') {
                delete this.pageparm.result
            }
        },
        // 
        resetForm(formName) {
            this.$refs[formName].resetFields();
            this.timeList = []
            delete this.pageparm.startTime
            delete this.pageparm.endTime
            if (this.pageparm.carNumbers) {
                delete this.pageparm.carNumbers
            }
            this.carNumbers = []
        },
        getTeams() {
            axios({
                url: baseURL + '/teams/selectall'
            }).then(res => {
                this.teamsList = res.data.data
                this.loading = false
            }).catch(err => {
                console.log(err);
            })
        },
        search() {
            if (this.carNumbers.length) {
                this.pageparm.carNumbers = this.carNumbers
            }

            this.loading = true
            if (this.timeList.length) {
                this.pageparm.startTime = this.getYMDHMS(new Date(this.timeList[0].toString()));
                this.pageparm.endTime = this.getYMDHMS(new Date(this.timeList[1].toString()))
            }
            this.pageparm.pageNo = 1
            this.pageparm.pageSize = 100000
            axios({
                method: 'post',
                url: baseURL + '/tedsfault/screenfault',
                data: this.pageparm
            }).then(res => {

                this.tableData = res.data.data.list
                this.tableData = this.tableData.map(item => {
                    item.deliveryDate = this.getYMDHMS(item.deliveryDate)
                    item.risk = item.risk == '0' ? 0 : parseInt(item.risk)
                    item.code = item.code == 'null' ? '' : item.code
                    item.schedule = item.schedule == 'null' ? '' : item.schedule
                    item.respDepart = item.respDepart == 'null' ? '' : item.respDepart
                    item.notes = item.notes == 'null' ? '' : item.notes
                    return item
                })
                this.total = res.data.data.size
                this.loading = false
                this.$message.success('')
            }).catch(err => {
                this.initPage()
                this.getFault()
                this.$message.error('')
                console.log(err)
            })
        },
        initPage() {
            this.pageparm = {
                pageNo: 1,
                pageSize: 100000,
                state: 0
            }
        },
        getYMDHMS(val) {
            let date = new Date(val)
            let Y = date.getFullYear()
            const M = (date.getMonth() + 1).toString().padStart(2, '0')
            const D = (date.getDate()).toString().padStart(2, '0')
            return Y + "-" + M + "-" + D;
        },
        exportExcel() {
            if (this.pageparm.result == '') {
                delete this.pageparm.result
            }
            if (this.carNumbers.length) {
                this.pageparm.carNumbers = this.carNumbers
            }
            if (this.timeList.length) {
                this.pageparm.startTime = this.getYMDHMS(new Date(this.timeList[0].toString()));
                this.pageparm.endTime = this.getYMDHMS(new Date(this.timeList[1].toString()))
            }

            let openUrl = `${baseURL}/tedsfault/export?`;

            for (let obj in this.pageparm) {
                if (this.pageparm[obj]) {
                    openUrl += obj + '=' + this.pageparm[obj] + '&'
                }
            }
            console.log(openUrl)

            axios.get(openUrl).then(res => {
                console.log(openUrl)
                window.location.href = openUrl
            })

        },
        callFather(val) {
            this.pageparm.pageNo = val.pageNo
            this.pageparm.pageSize = val.pageSize
            this.getFault()
        },
        getFault() {
            if (this.carNumbers.length) {
                this.pageparm.carNumbers = this.carNumbers
            }
            axios({
                url: baseURL + '/tedsfault/screenfault',
                method: 'post',
                data: this.pageparm
            }).then(res => {
                this.tableData = res.data.data.list || []
                this.tableData = this.tableData.map(item => {
                    item.deliveryDate = this.getYMDHMS(item.deliveryDate)
                    item.risk = item.risk == '0' ? parseInt(0) : parseInt(item.risk)
                    item.code = item.code == 'null' ? '' : item.code
                    item.schedule = item.schedule == 'null' ? '' : item.schedule
                    item.respDepart = item.respDepart == 'null' ? '' : item.respDepart
                    item.notes = item.notes == 'null' ? '' : item.notes
                    return item
                })
                this.total = res.data.data.size
                this.loading = false
            }).catch(err => {
                console.log(err);
            })
        },
        deleteById(row) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                axios({
                    url: baseURL + '/tedsfault/deletefaultid',
                    params: { id: row.id }
                }).then(res => {
                    this.$message({
                        message: '',
                        type: 'success'
                    });
                    this.getFault()
                }).catch(err => {
                    console.log(err)
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        handleEdit(index, row) {
            this.editFormVisible = true
            if (row != undefined && row != 'undefined') {
                this.title = ''
                this.editForm = row
                if (this.editForm.result != '' & this.editForm.result != '') {
                    this.result = ''
                } else if (this.editForm.result == '') {
                    this.result = ''
                } else if (this.editForm.result == "") {
                    this.result = ''
                }
                this.risk = row.risk
            } else {
                this.title = ''
                this.editForm = {}
                this.result = ''
            }
        },
        changeResult() {
            this.editForm.result = ''
        },
        handelFault() {
            this.$refs['editForm'].validate((valid) => {
                if (valid) {
                    if (this.result != '') {
                        this.editForm.result = this.result
                    }
                    this.editForm.risk = this.risk
                    if (this.title == '') {
                        this.editForm.dimension = "0"
                        axios({
                            url: baseURL + '/tedsfault/insertfault',
                            method: 'post',
                            data: this.editForm
                        }).then(res => {
                            this.$message({
                                message: this.title + '',
                                type: 'success'
                            });
                            this.editFormVisible = false
                            this.editForm = {}
                            this.risk = 0
                            this.initPage()
                            this.getFault()
                        }).catch(err => {
                            console.log(err)
                            this.$message({
                                message: this.title + '',
                                type: 'error'
                            });
                            this.editFormVisible = false
                            this.editForm = {}
                            this.risk = 0
                            this.initPage()
                            this.getFault()
                        })
                    } else {
                        for (let obj in this.editForm) {
                            if (!this.editForm[obj] & obj != 'risk') {
                                this.editForm[obj] = 'null'
                            }
                        }
                        axios({
                            url: baseURL + '/tedsfault/updatefaultid',
                            method: 'post',
                            data: this.editForm,
                        }).then(res => {
                            this.editFormVisible = false
                            this.editForm = {}
                            this.risk = 0
                            this.$message.success('')
                        }).catch(err => {
                            console.log(err)
                            this.$message.error("")
                        })
                    }
                } else {
                    console.log('');
                    return false;
                }
            });

        },
        closeDialog() {
            this.editFormVisible = false
            this.editForm = {}
            this.risk = 0
            this.initPage()
            this.getFault()
        },
        handleUploadSuccess(res) {
            this.uploadUrlPath = JSON.stringify(res);
            this.loading.close();
        },
        handleBeforeUpload() {
            this.loading = this.$loading({
                lock: true,
                text: "",
                background: "rgba(0, 0, 0, 0.7)",
            });
        },
        handleUploadError() {
            this.$message({
                type: "error",
                message: "",
            });
            this.loading.close();
        },

        handleRemove(file, fileList) {
            console.log(file, fileList);

        },
        handlePreview(file) {
            console.log(file);
        },
        handleExceed(files, fileList) {
            this.$message.warning(` ${files.length}  ${files.length + fileList.length} `);
        },
        beforeRemove(file, fileList) {
            return this.$confirm(` ${file.name}`);
        },
        handleSuccess() {
            this.getFault()
            this.$message.success("")
        },
        handleError() {
            this.getFault()
            this.$message.error("")

        },
        resetRisk() {
            this.risk = 0
        },
        changState(id, state) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                axios({
                    url: ` ${baseURL}/tedsfault/changestate`,
                    params: { id, state: 1 }
                }).then(res => {
                    this.$message.success("")
                    this.search()
                }).catch(err => {
                    this.$message.error('')
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        changeTrack() {
            this.$refs['editTrackForm'].validate((valid) => {
                if (valid) {
                    this.$confirm(', ?', '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                    }).then(() => {
                        axios({
                            url: ` ${baseURL}/tedsfault/changestate`,
                            params: {
                                id: this.id,
                                state: 2,
                                trackPerson: this.editTrackForm.trackPerson,
                                trackRequire: this.editTrackForm.trackRequire,
                                feedback: this.editTrackForm.feedback
                            }
                        }).then(res => {
                            this.$message.success("")
                            this.search()
                            this.dialogTrackVisible = false
                            this.editTrackForm = {}
                        }).catch(err => {
                            this.$message.error('')
                            this.editTrackForm = {}
                        })
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: ''
                        });
                    });
                } else {
                    console.log('');
                    return false;
                }
            });
        },
        changeDimension(id, dimension) {
            this.$confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                axios({
                    url: ` ${baseURL}/tedsfault/changedimension`,
                    params: { id, dimension }
                }).then(res => {
                    this.$message.success("")
                    this.search()
                }).catch(err => {
                    this.$message.error('')
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        handleTrack(id) {
            this.dialogTrackVisible = true
            this.id = id
        },
        handleClose() {
            this.editTrackForm = {}
            this.dialogTrackVisible = false
        }
    }
}
</script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.rate_wrap {
    display: flex;
    align-items: center;
}
</style>
  


