<template>
    <div>
        <el-page-header @back="goBack" content="">
        </el-page-header>
        <br>
        <quill-editor ref="myQuillEditor" v-model="content" :options="editorOption" @blur="onEditorBlur($event)"
            @focus="onEditorFocus($event)" @ready="onEditorReady($event)" @change="onEditorChange($event)" />
        <el-button size="small" type="primary" class="title" @click="handelFault()"></el-button>
    </div>
</template>
  
<script>
import axios from 'axios'
import { baseURL } from '../../../api/base'

import Quill from 'quill'

import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";
import { quillEditor } from "vue-quill-editor";
// 
import ImageResize from "quill-image-resize-module";
// 
Quill.register("modules/imageResize", ImageResize);
// 
import { ImageDrop } from 'quill-image-drop-module';
// 
Quill.register('modules/imageDrop', ImageDrop);

export default {
    components: { quillEditor },
    name: "Editor",
    props: {
        /*  */
        value: {
            type: String,
            default: "",
        },
        /*  */
        height: {
            type: Number,
            default: null,
        },
        /*  */
        minHeight: {
            type: Number,
            default: null,
        },
        /*  */
        readOnly: {
            type: Boolean,
            default: false,
        },
        // (MB)
        fileSize: {
            type: Number,
            default: 5,
        },
        /* base64url */
        type: {
            type: String,
            default: "url",
        }
    },
    data() {
        return {
            baseURL,
            id: 0,
            editForm: {},
            msg: '',
            // 
            content: "",
            editorOption: {
                placeholder: "...",
                modules: {
                    // 
                    imageDrop: true,
                    // 
                    imageResize: {
                        displayStyles: {
                            backgroundColor: "black",
                            border: "none",
                            color: "white",
                        },
                        modules: ["Resize", "DisplaySize", "Toolbar"],
                    },
                    // 
                    toolbar: [
                        [
                            "bold",
                            "italic",
                            "underline",
                            "strike",
                            "blockquote",
                            "code-block",
                            { header: 1 },
                            { header: 2 },
                            { list: "ordered" },
                            { list: "bullet" },
                            { indent: "-1" },
                            { indent: "+1" },
                            { align: [] },
                            { color: [] },
                            { background: [] },
                            "image",
                        ],
                        // 
                        [{ size: ['small', false, 'large', 'huge'] }],
                        // 
                        [{ header: [1, 2, 3, 4, 5, 6, false] }],
                    ],
                },
            },
        };
    },
    created() {
        this.id = this.$route.query.id
        console.log(this.id)
        this.getRich()
    },
    methods: {
        // 
        onEditorBlur(val) { },
        // 
        onEditorFocus() { },
        // 
        onEditorReady() { },
        // 
        onEditorChange() { },
        goBack() {
            this.$router.back(-1)
        },
        getRich() {
            axios({
                method: 'post',
                url: baseURL + '/fault/screenfault',
                data: {
                    pageNo: 1,
                    pageSize: 100000,
                    id: this.id
                }
            }).then(res => {
                let rich = res.data.data.list[0].rich
                this.editForm = res.data.data.list[0]
                if (rich) {
                    this.content = rich
                }
            }).catch(err => {
                this.initPage()
                this.getFault()
                this.$message.error('')
                console.log(err)
            })
        },
        handelFault() {
            this.editForm = { ...this.editForm, rich: this.content }
            axios({
                url: baseURL + '/fault/updatefaultid',
                method: 'post',
                data: this.editForm,
            }).then(res => {
                this.editFormVisible = false
                this.editForm = {}
                this.risk = 0
                this.$message.success('')
            }).catch(err => {
                console.log(err)
                this.$message.error("")
            })
        }
    },
    watch: {
        // 
        content() { },
    },
    mounted() {
        const titleConfig = [
            { Choice: ".ql-insertMetric", title: "" },
            { Choice: ".ql-bold", title: "" },
            { Choice: ".ql-italic", title: "" },
            { Choice: ".ql-underline", title: "" },
            { Choice: ".ql-header", title: "" },
            { Choice: ".ql-strike", title: "" },
            { Choice: ".ql-blockquote", title: "" },
            { Choice: ".ql-code", title: "" },
            { Choice: ".ql-code-block", title: "" },
            { Choice: ".ql-font", title: "" },
            { Choice: ".ql-size", title: "" },
            { Choice: '.ql-list[value="ordered"]', title: "" },
            { Choice: '.ql-list[value="bullet"]', title: "" },
            { Choice: ".ql-direction", title: "" },
            { Choice: '.ql-header[value="1"]', title: "h1" },
            { Choice: '.ql-header[value="2"]', title: "h2" },
            { Choice: ".ql-align", title: "" },
            { Choice: ".ql-color", title: "" },
            { Choice: ".ql-background", title: "" },
            { Choice: ".ql-image", title: "" },
            { Choice: ".ql-video", title: "" },
            { Choice: ".ql-link", title: "" },
            { Choice: ".ql-formula", title: "" },
            { Choice: ".ql-clean", title: "" },
            { Choice: '.ql-script[value="sub"]', title: "" },
            { Choice: '.ql-script[value="super"]', title: "" },
            { Choice: '.ql-indent[value="-1"]', title: "" },
            { Choice: '.ql-indent[value="+1"]', title: "" },
            { Choice: ".ql-header .ql-picker-label", title: "" },
            { Choice: '.ql-header .ql-picker-item[data-value="1"]', title: "" },
            { Choice: '.ql-header .ql-picker-item[data-value="2"]', title: "" },
            { Choice: '.ql-header .ql-picker-item[data-value="3"]', title: "" },
            { Choice: '.ql-header .ql-picker-item[data-value="4"]', title: "" },
            { Choice: '.ql-header .ql-picker-item[data-value="5"]', title: "" },
            { Choice: '.ql-header .ql-picker-item[data-value="6"]', title: "" },
            { Choice: ".ql-header .ql-picker-item:last-child", title: "" },
            { Choice: '.ql-size .ql-picker-item[data-value="small"]', title: "" },
            { Choice: '.ql-size .ql-picker-item[data-value="large"]', title: "" },
            { Choice: '.ql-size .ql-picker-item[data-value="huge"]', title: "" },
            { Choice: ".ql-size .ql-picker-item:nth-child(2)", title: "" },
            { Choice: ".ql-align .ql-picker-item:first-child", title: "" },
            { Choice: '.ql-align .ql-picker-item[data-value="center"]', title: "" },
            { Choice: '.ql-align .ql-picker-item[data-value="right"]', title: "" },
            { Choice: '.ql-align .ql-picker-item[data-value="justify"]', title: "" },
            { Choice: ".ql-upload", title: "" },
            { Choice: ".ql-table", title: "" },
            { Choice: ".ql-table-insert-row", title: "" },
            { Choice: ".ql-table-insert-column", title: "" },
            { Choice: ".ql-table-delete-row", title: "" },
            { Choice: ".ql-table-delete-column", title: "" },
        ];
        for (let item of titleConfig) {
            let tip = document.querySelector(".quill-editor " + item.Choice);
            if (!tip) continue;
            tip.setAttribute("title", item.title);
        }
    }

};
</script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
/* .ql-editor {
  height: 300px !important;
 } */
.rate_wrap {
    display: flex;
    align-items: center;
}

/*  */
.ql-snow .ql-picker.ql-size .ql-picker-label::before,
.ql-snow .ql-picker.ql-size .ql-picker-item::before {
    content: "14px";
}

.ql-snow .ql-picker.ql-size .ql-picker-label[data-value="small"]::before,
.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="small"]::before {
    content: "10px";
}

.ql-snow .ql-picker.ql-size .ql-picker-label[data-value="large"]::before,
.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="large"]::before {
    content: "18px";
}

.ql-snow .ql-picker.ql-size .ql-picker-label[data-value="huge"]::before,
.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="huge"]::before {
    content: "32px";
}

.ql-snow .ql-picker.ql-header .ql-picker-label::before,
.ql-snow .ql-picker.ql-header .ql-picker-item::before {
    content: "";
}

.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="1"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="1"]::before {
    content: "1";
}

.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="2"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="2"]::before {
    content: "2";
}

.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="3"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="3"]::before {
    content: "3";
}

.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="4"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="4"]::before {
    content: "4";
}

.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="5"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="5"]::before {
    content: "5";
}

.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="6"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="6"]::before {
    content: "6";
}

.ql-snow .ql-picker.ql-font .ql-picker-label::before,
.ql-snow .ql-picker.ql-font .ql-picker-item::before {
    content: "";
}

.ql-snow .ql-picker.ql-font .ql-picker-label[data-value="serif"]::before,
.ql-snow .ql-picker.ql-font .ql-picker-item[data-value="serif"]::before {
    content: "";
}

.ql-snow .ql-picker.ql-font .ql-picker-label[data-value="monospace"]::before,
.ql-snow .ql-picker.ql-font .ql-picker-item[data-value="monospace"]::before {
    content: "";
}

/*  */
.button {
    margin-top: 10px;
}
</style>
  


