    <!--  -->
    <el-form :inline="true" :model="pageparm" class="user-search" ref="searchForm">
      <el-form-item label="" prop="dimension">
        <el-select v-model="pageparm.dimension" placeholder="" @change="changeInput($event)">
          <el-option label="" value="0"></el-option>
          <el-option label="" value="1"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="batch">
        <el-input v-model="pageparm.batch" placeholder="" @input="changeInput($event)"></el-input>
      </el-form-item>
      <el-form-item label="" prop="headDocumentNumber">
        <el-input v-model="pageparm.headDocumentNumber" placeholder="" @input="changeInput($event)"></el-input>
      </el-form-item>
      <el-form-item label="" prop="roadDocumentNumber">
        <el-input v-model="pageparm.roadDocumentNumber" placeholder="" @input="changeInput($event)"></el-input>
      </el-form-item>
      <el-form-item label="" prop="applicNumber">
        <el-input v-model="pageparm.applicNumber" placeholder="" @input="changeInput($event)"></el-input>
      </el-form-item>
      <el-form-item label="" prop="entryName">
        <el-input v-model="pageparm.entryName" placeholder="" @input="changeInput($event)"></el-input>
      </el-form-item>
      <el-form-item label="" prop="state">
        <el-select v-model="pageparm.state" placeholder="" @change="changeInput($event)">
          <el-option label="" :value="1"></el-option>
          <el-option label="" :value="0"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="repairProcedure">
        <el-input v-model="pageparm.repairProcedure" placeholder="" @input="changeInput($event)"></el-input>
      </el-form-item>
      <el-form-item label="" prop="carNumber">
        <el-select multiple filterable v-model="carNumbers" placeholder=""  @change="changeInput($event)">
          <el-option v-for="item in squareCarNumberList" :key="item.value" :label="item.value" :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="complTime">
        <el-select v-model="pageparm.complTime" placeholder="" @change="changeInput($event)">
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
      </el-form-item>

      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-search" @click="search()"></el-button>
        <el-button size="small" type="primary" icon="el-icon-refresh-right"
          @click="resetForm('searchForm')"></el-button>
        <el-button size="small" type="primary" icon="el-icon-plus" @click="handleEdit()"></el-button>
      </el-form-item>
    </el-form>

    <!--  -->
    <el-table :data="tableData" style="width: 100%" border v-loading="loading" height="650" :row-style="rowStyle">
      <el-table-column type="index" label="" align="center" width="70" :index="indexMethod" fixed></el-table-column>
      <el-table-column prop="batch" label="" width="80" align="center"></el-table-column>
      <el-table-column prop="headDocumentNumber" label="" width="120" align="center"></el-table-column>
      <el-table-column prop="roadDocumentNumber" label="" width="120" align="center"></el-table-column>
      <el-table-column prop="applicNumber" label="" width="120" align="center"></el-table-column>
      <el-table-column prop="entryName" label="" width="200" align="center"></el-table-column>
      <el-table-column prop="repairProcedure" label="" width="200" align="center"></el-table-column>
      <el-table-column prop="total" label="" align="center" width="70"></el-table-column>
      <el-table-column prop="complEight" label="CRH380A" width="90" align="center"></el-table-column>
      <el-table-column prop="complTwo" label="CRH2A" width="90" align="center"></el-table-column>
      <el-table-column prop="complTotal" label="" width="70" align="center"></el-table-column>
      <el-table-column prop="incomplEight" label="CRH380A" width="90" align="center"></el-table-column>
      <el-table-column prop="incomplTwo" label="CRH2A" width="90" align="center"></el-table-column>
      <el-table-column prop="incomplTotal" label="" width="70" align="center"></el-table-column>

      <el-table-column prop="carNumbers" label="" align="center" width="280">
        <template slot-scope="scope">
          <div v-for="tag in scope.row.sourceCarNumberDto" :key="tag.cid" style="display: inline-block;">
            <el-tag v-if="tag.complTime == '' & tag.concern == 0" type="info">{{ tag.carNumber }}</el-tag>
            <el-tag v-else-if="tag.complTime != '' & tag.concern == 0" type="success">{{ tag.carNumber }}</el-tag>
            <el-tag v-else type="warning">{{ tag.carNumber }}</el-tag>
          </div>
        </template>

      </el-table-column>

      <el-table-column prop="note" label="" align="center" width="220"></el-table-column>

      <!-- <el-table-column prop="notes" label="" align="center" width="130">
        <template slot-scope="scope">
          <el-button @click="showModal(scope.row.sid)" class="icon-button">
            <i class="el-icon-view"></i>
            
          </el-button>
        </template>
        <el-dialog title="" :visible.sync="dialogVisible" width="40%" :before-close="handleCloseFile" append-to-body>
          <el-table :data="attachmentList" style="width: 100%">
            <el-table-column prop="fileName" align="center" label="">
              <template slot-scope="{ row }">
                <el-link @click="handleFileNameClick(row)">{{ row.fileName }}</el-link>
              </template>
            </el-table-column>
            <el-table-column prop="fileType" align="center" label=""></el-table-column>
            <el-table-column label="" align="center">
              <template slot-scope="{ row }">
                <el-button type="primary" icon="el-icon-download" @click="downloadAttachment(row)"></el-button>
                <el-button type="danger" icon="el-icon-delete" @click="deleteAttachment(row)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-dialog>

      </el-table-column> -->
      <!-- <el-table-column prop="" label="" width="120" align="center" fixed="right">
        <template slot-scope="scope">
          <div style="margin-top: 5px;">
            <el-button @click="showUploadModal(scope.row)" size="small" type="primary"></el-button>
          </div>
        </template>
      </el-table-column> -->

      <!--  -->
      <el-dialog title="" :visible.sync="uploadModalVisible" width="650px" :before-close="beforeClose" append-to-body
        :show-close="false" :close-on-click-modal="false">
        <el-upload class="uploadFile" :action="`${baseURL}/source/upload`" :on-success="handleFileSuccess"
          :on-error="handleFileError" :file-list="fileList" :data="source" ref="refName" drag multiple>
          <i class="el-icon-upload"></i>
          <div class="el-upload__text"><em></em></div>
        </el-upload>
        <div slot="footer" class="dialog-footer">
          <el-button @click="upload"> </el-button>
          <el-button type="primary" @click="upload"> </el-button>
        </div>
      </el-dialog>

      <el-table-column prop="" label="" width="180" align="center" fixed="right">
        <template slot-scope="scope">
          <div>
            <el-button type="primary" icon="el-icon-circle-plus" circle
              @click="handleCarNumbersInfo(scope.row)"></el-button>
            <el-button type="danger" icon="el-icon-remove" circle @click="deleteCarNumbersInfo(scope.row)"></el-button>
          </div>
          <div style="margin-top: 10px;">
            <el-button type="primary" icon="el-icon-edit" circle @click="handleEdit(scope.$index, scope.row)"></el-button>
            <el-button type="danger" icon="el-icon-delete" circle
              @click="deleteSourceInfoBySID(scope.row.sid)"></el-button>
          </div>
          <div style="margin-top: 10px;">
            <el-button type="warning" v-if="scope.row.dimension == 1" icon="el-icon-warning" circle
              @click="changeDimension(scope.row.sid, 0)"></el-button>
            <el-button v-else="scope.row.dimension == 0" icon="el-icon-warning-outline" circle
              @click="changeDimension(scope.row.sid, 1)"></el-button>
          </div>
        </template>
      </el-table-column>

    </el-table>

    <!--  -->
    <el-dialog :title="title" :visible.sync="editFormVisible" width="50%" @click="closeDialog('editForm')">
      <el-form label-width="120px" :model="editForm" ref="editForm">
        <el-form-item label="" prop="batch">
          <el-input v-model="editForm.batch" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="headDocumentNumber">
          <el-input v-model="editForm.headDocumentNumber" :min="1" label="" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="roadDocumentNumber">
          <el-input v-model="editForm.roadDocumentNumber" label="" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="applicNumber">
          <el-input v-model="editForm.applicNumber" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="entryName">
          <el-input type="textarea" v-model="editForm.entryName" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="repairProcedure">
          <el-input v-model="editForm.repairProcedure" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="total">
          <el-input-number :min="0" :max="1000" v-model="editForm.total" auto-complete="off"
            placeholder=""></el-input-number>
        </el-form-item>
        <el-form-item label="CRH380A" prop="complEight">
          <el-input-number :min="0" :max="1000" v-model="editForm.complEight" label=""
            placeholder="CRH380A"></el-input-number>
        </el-form-item>
        <el-form-item label="CRH2A" prop="complTwo">
          <el-input-number :min="0" :max="1000" v-model="editForm.complTwo" label=""
            placeholder="CRH2A"></el-input-number>
        </el-form-item>
        <el-form-item label="" prop="complTotal">
          <el-input-number :min="0" :max="1000" v-model="editForm.complTotal" auto-complete="off"
            placeholder=""></el-input-number>
        </el-form-item>
        <el-form-item label="CRH380A" prop="incomplEight">
          <el-input-number :min="0" :max="1000" v-model="editForm.incomplEight" label=""
            placeholder="CRH380A"></el-input-number>
        </el-form-item>
        <el-form-item label="CRH2A " prop="incomplTwo">
          <el-input-number :min="0" :max="1000" v-model="editForm.incomplTwo" label=""
            placeholder="CRH2A"></el-input-number>
        </el-form-item>
        <el-form-item label="" prop="incomplTotal">
          <el-input-number v-model="editForm.incomplTotal" auto-complete="off" placeholder=""></el-input-number>
        </el-form-item>

        <el-form-item label="" prop="node">
          <el-input type="textarea" v-model="editForm.note" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="closeDialog('editForm')"></el-button>
        <el-button size="small" type="primary" class="title" @click="handelSource('editForm')"></el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="carNumbersInfoVisible" width="50%" :before-close="closeCarNumbersInfoVisible">
      <div>
        <el-form :inline="true" :model="pageparmcar" class="user-search" ref="searchForm">
          <el-form-item label="" prop="">
            <el-select v-model="selectSquareCarNumberList" multiple placeholder="">
              <el-option v-for="item in squareCarNumberList" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button size="small" type="primary" icon="el-icon-search" @click="insertLongCarNumber()"></el-button>
          </el-form-item>
        </el-form>
        <el-table :data="carNumbersInfoData" style="width: 100%" border v-loading="loading" height="500"
          :row-style="rowCarStyle">
          <el-table-column type="index" label="" align="center" width="70" :index="indexMethod" fixed></el-table-column>
          <el-table-column prop="carNumber" label="" width="180" align="center"></el-table-column>
          <el-table-column prop="complTime" label="" width="240" align="center">
            <template slot-scope="scope">
              <el-input type="textarea" class="input-new-tag" v-model="scope.row.complTime" ref="saveTagInput"
                @blur="editComplTime(scope.row)"></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="" label="" align="center">
            <template slot-scope="scope">
              <el-button type="warning" v-if="scope.row.concern == 1" icon="el-icon-warning" circle
                @click="changeCarConcern(scope.row.cid, 0)"></el-button>
              <el-button v-else="scope.row.concern == 0" icon="el-icon-warning-outline" circle
                @click="changeCarConcern(scope.row.cid, 1)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="closeCarNumbersInfoVisible()"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="deleteCarNumbersInfoVisible" width="50%"
      :before-close="() => { deleteCarNumbersInfoVisible = false }">
      <div>
        <el-table :data="carNumbersInfoData" style="width: 100%" border v-loading="loading" height="500"
          :row-style="rowCarStyle">
          <el-table-column type="index" label="" align="center" width="70" :index="indexMethod" fixed></el-table-column>
          <el-table-column prop="carNumber" label="" width="180" align="center"></el-table-column>
          <el-table-column prop="complTime" label="" width="240" align="center">
            <template slot-scope="scope">
              <el-input type="textarea" disabled class="input-new-tag" v-model="scope.row.complTime" ref="saveTagInput"
                @blur="editComplTime(scope.row)"></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="" label="" align="center">
            <template slot-scope="scope">
              <el-button type="danger" icon="el-icon-delete" circle
                @click="deleteCarNumber(scope.row.cid, scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="deleteCarNumbersInfoVisible = false"> </el-button>
      </span>
    </el-dialog>

  </div>
</template>
  
<script>
import axios from 'axios'
import { baseURL } from '../../../api/base'
import { carNumberList } from '../.././../common/carNumber'
import { classCarNumbers } from '../.././../common/classCarNumbers'
import { squareCarNumberList, twoCarNumberList, eightCarNumberList } from '../../../common/squareCarnumbers'
import { downloadFile } from '../../../api/FileDownload'


export default {
  name: 'qualitySquare',
  data() {
    return {
      baseURL,
      carNumberList,
      classCarNumbers,
      squareCarNumberList,
      twoCarNumberList,
      eightCarNumberList,
      selectSquareCarNumberList: [],
      sid: 0,
      carNumbers: [],
      tableData: [],
      loading: false,
      pageparm: {
        type: 1,
        pageNo: 1,
        pageSize: 100000
      },
      pageparmcar: {},
      editFormVisible: false,
      title: '',
      editForm: {},
      total: 0,
      loading: false,
      carNumbersVisible: false,

      props: { multiple: true },

      selectedCarNumbers: [],
      inputVisible: false,
      inputValue: '',

      carNumberComplTimeVisible: false,
      carNumberComplForm: {},

      carNumbersBySid: [],

      carNumbersInfoVisible: false,
      carNumbersInfoData: [],

      ComplTimeVisible: true,

      deleteCarNumbersInfoVisible: false,

      selectLongCarNumberList: [],

      updateTotal: 0,
      updateIncompl: 0,
      updateCompl: 0,
      updateIncomplTwo: 0,
      updateIncomplEight: 0,
      updateComplTwo: 0,
      updateComplEight: 0,

      span: [],
      index: null,

      uploadModalVisible: false,
      fileList: [],
      dialogVisible: false,
      attachmentList: [
        // 
        // { name: '1', type: '', url: '' }
      ],
      previewDialogVisible: false,
      previewFile: '',
      source: {},
      deleteParams: {},

    }
  },
  computed: {
    indexMethod: function () {
      return (this.pageparm.pageNo - 1) * (this.pageparm.pageSize) + 1
    },
  },
  created() {
    this.getQS()
  },
  methods: {
    changeInput(e) {
      this.$forceUpdate();
    },
    rowStyle({ row }) {
      if (row.dimension == 1) {
        return { 'background': 'antiquewhite' }
      }
    },
    search() {
      if (this.carNumbers.length) {
        this.pageparm.carNumbers = this.carNumbers
      }
      this.loading = true
      axios({
        method: 'post',
        url: baseURL + '/source/selectallsourceinfo',
        data: this.pageparm
      }).then(res => {
        let tableData = res.data.data
        tableData = tableData.map(item => {
          (item.sourceCarNumberDto).sort(this.compare('carNumber'))
          return item
        })
        this.tableData = tableData
        this.loading = false
        this.$message.success('')
      }).catch(err => {
        this.initPage()
        this.$message.error('')
        console.log(err)
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.carNumbers = []
      if (this.pageparm.carNumbers) {
        delete this.pageparm.carNumbers
      }
    },
    initPage() {
      this.pageparm = {
        pageNo: 1,
        pageSize: 100000,
        type: 1
      }
    },
    getQS() {
      axios({
        url: baseURL + '/source/selectallsourceinfo',
        method: 'post',
        data: this.pageparm
      }).then(res => {
        let tableData = res.data.data
        tableData = tableData.map(item => {
          (item.sourceCarNumberDto).sort(this.compare('carNumber'))
          return item
        })
        this.tableData = tableData

        this.loading = false
      }).catch(err => {
        console.log(err);
      })
    },
    compare(property) {
      return function (a, b) {
        return a[property] - b[property];
      }
    },
    handleEdit(index, row) {
      this.editFormVisible = true
      if (row != undefined && row != 'undefined') {
        this.title = ''
        this.editForm = row
      } else {
        this.title = ''
        this.editForm = {}
      }
    },
    changeResult() {
      this.editForm.result = ''
    },
    handelSource() {
      this.$refs['editForm'].validate((valid) => {
        if (valid) {
          if (this.title == '') {
            axios({
              url: baseURL + '/source/insertsource',
              method: 'post',
              data: this.editForm
            }).then(res => {
              this.$message({
                message: this.title + '',
                type: 'success'
              });
              this.editFormVisible = false
              this.editForm = {}
              this.getQS()
            }).catch(err => {
              console.log(err)
              this.$message({
                message: this.title + '',
                type: 'error'
              });
              this.editFormVisible = false
              this.editForm = {}
            })
          } else {
            axios({
              url: baseURL + '/source/updatesourceid',
              method: 'post',
              data: this.editForm,
            }).then(res => {
              this.editFormVisible = false
              this.editForm = {}
              this.$message.success('')
            }).catch(err => {
              console.log(err)
              this.$message.error("")
            })
          }
        } else {
          console.log('');
          return false;
        }
      });
    },
    callFather(val) {
      this.pageparm.pageNo = val.pageNo
      this.pageparm.pageSize = val.pageSize
    },
    closeDialog() {
      this.editFormVisible = false
      this.editForm = {}
    },
    handleCarNumbers(row) {
      this.editForm = row
      this.sid = row.sid
      this.getCarNumbersBySid(row.sid)
      this.carNumbersVisible = true
    },
    getCarNumbersBySid(sid) {
      axios({
        method: 'post',
        url: baseURL + '/source/selectallsourceinfo',
        data: { sid, ...this.pageparm }
      }).then(res => {
        this.carNumbersBySid = res.data.data[0].sourceCarNumberDto
        this.carNumbersInfoData = res.data.data[0].sourceCarNumberDto
        this.carNumbersInfoData.sort(this.compare('carNumber'))
      }).catch(err => {

      })
    },
    insertLongCarNumber() {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        if (this.selectSquareCarNumberList.length) {
          for (let i = 0; i < this.selectSquareCarNumberList.length; i++) {
            axios({
              url: baseURL + '/source/insertsourcecarnumber',
              method: 'post',
              data: { sid: this.sid, carNumber: this.selectSquareCarNumberList[i] },
            }).then(res => {
              this.getQS()
              this.getCarNumbersBySid(this.sid)

            }).catch(err => {
              this.getQS()
              this.getCarNumbersBySid(this.sid)
              console.log(err)
              this.$message.error("")
            })
          }
          this.selectSquareCarNumberList = []
          // this.updateNumber(this.sid)

        } else {
          this.$message.warning("")
        }

      }).then(() => {
        this.updateNumber(this.sid)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      })
    },
    toRemoveSpacing(str) {
      var reg = /[\t\r\f\n\s]*/g;
      if (typeof str === 'string') {
        var trimStr = str.replace(reg, '');
      }
      return trimStr
    },
    updateNumber(sid) {
      axios({
        method: 'post',
        url: baseURL + '/source/selectallsourceinfo',
        data: { sid }
      }).then(res => {
        let sourceCarNumberDto = res.data.data[0].sourceCarNumberDto
        let total = sourceCarNumberDto.length
        this.updateTotal = total
        let incompl = 0
        let compl = 0

        let incomplTwo = 0
        let incomplEight = 0
        let complTwo = 0
        let complEight = 0

        for (let i = 0; i < total; i++) {
          let complTime = this.toRemoveSpacing(sourceCarNumberDto[i].complTime)
          if (complTime.toLowerCase() == 'null' || complTime == '' || complTime == 'N/A' || complTime == '') {
            incompl += 1
            // 2A
            if (twoCarNumberList.includes(sourceCarNumberDto[i].carNumber)) {
              incomplTwo += 1
            } else {
              incomplEight += 1
            }
            // 2A
          } else {
            compl += 1
            if (twoCarNumberList.includes(sourceCarNumberDto[i].carNumber)) {
              complTwo += 1
            } else {
              complEight += 1
            }
          }
        }
        this.updateIncompl = incompl
        this.updateCompl = compl
        this.updateComplEight = complEight
        this.updateComplTwo = complTwo
        this.updateIncomplTwo = incomplTwo
        this.updateIncomplEight = incomplEight

      }).then(() => {
        let state
        if (this.updateIncompl == 0 & this.updateTotal != 0) {
          state = 1
        } else {
          state = 0
        }
        axios({
          url: baseURL + '/source/updatesourceid',
          method: 'post',
          data: {
            sid,
            ...this.editForm,
            total: this.updateTotal,
            incomplTotal: this.updateIncompl,
            complTotal: this.updateCompl,
            complEight: this.updateComplEight,
            complTwo: this.updateComplTwo,
            incomplEight: this.updateIncomplEight,
            incomplTwo: this.updateIncomplTwo,
            state
          },
        }).then(res => {
          this.editFormVisible = false
          // this.initPage()
          this.search()
          this.getCarNumbersBySid(this.sid)
        }).catch(err => {
          console.log(err)
          // this.$message.error("")
        })
      })

    },
    handleCarNumberForm() {
      if (!this.inputValue) {
        return
      }
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {

        let inputValue = this.inputValue;
        if (inputValue) {
          this.selectedCarNumbers.push(inputValue);
          axios({
            url: baseURL + '/source/insertsourcecarnumber',
            method: 'post',
            data: { sid: this.sid, carNumber: this.inputValue },
          }).then(res => {
            this.getQS()
            this.getCarNumbersBySid(this.sid)
            this.inputVisible = false;
            this.$message.success('')
          }).catch(err => {
            this.getQS()
            this.editForm = {}
            console.log(err)
            this.$message.error("")
          })
        }
        this.inputValue = '';

      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    handleClose() {
      this.carNumberComplForm = {}
      this.carNumberComplTimeVisible = false
    },
    handleSelectedCarNumbers() {
      this.carNumbersVisible = false
    },
    handleCloseNumbers(cid, tag) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        axios({
          url: baseURL + '/source/deletesourcecarnumbersid',
          method: 'post',
          params: { cid }
        }).then(res => {
          this.$message({
            message: '',
            type: 'success'
          });
          this.getQS()
        }).catch(err => {
          console.log(err)
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    deleteCarNumber(cid, row) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        axios({
          url: baseURL + '/source/deletesourcecarnumbersid',
          method: 'post',
          params: { cid }
        }).then(res => {
          this.$message({
            message: '',
            type: 'success'
          });
          this.getCarNumbersBySid(this.sid)
        }).then(() => {
          // total
          let total = this.editForm.total
          total = total - 1
          console.log(total)
          let incomplTotal
          let complTotal
          let state
          let editForm
          let complTime = this.toRemoveSpacing(row.complTime)
          let incomplTwo, incomplEight, complTwo, complEight
          if (complTime.toLowerCase() == 'null' || complTime == '' || complTime == 'N/A' || complTime == '') {
            incomplTotal = this.editForm.incomplTotal - 1
            if (total != 0 & incomplTotal == 0) {
              state = 1
            } else {
              state = 0
            }

            if (twoCarNumberList.includes(row.carNumber)) {
              incomplTwo = this.editForm.incomplTwo - 1
            } else {
              incomplEight = this.editForm.incomplEight - 1
            }

          } else {
            complTotal = this.editForm.complTotal - 1
            if (total == complTotal) {
              state = 1
              console.log(state)
            } else {
              state = 0
              console.log(state)
            }

            if (twoCarNumberList.includes(row.carNumber)) {
              complTwo = this.editForm.complTwo - 1
            } else {
              complEight = this.editForm.complEight - 1
            }
          }

          editForm = { ...this.editForm, total, incomplTotal, complTotal, state }
          if (complTime.toLowerCase() == 'null' || complTime == '' || complTime == 'N/A' || complTime == '') {
            if (twoCarNumberList.includes(row.carNumber)) {
              editForm = { ...this.editForm, total, incomplTotal, incomplTwo, state }
            } else {
              editForm = { ...this.editForm, total, incomplTotal, incomplEight, state }
            }

          } else {
            if (twoCarNumberList.includes(row.carNumber)) {
              editForm = { ...this.editForm, total, complTotal, complTwo, state }
            } else {
              editForm = { ...this.editForm, total, complTotal, complEight, state }
            }

          }


          axios({
            url: baseURL + '/source/updatesourceid',
            method: 'post',
            data: {
              sid: this.sid,
              ...editForm,

            },
          }).then(res => {
            this.initPage()
            this.getQS()
          }).then(() => {

            axios({
              method: 'post',
              url: baseURL + '/source/selectallsourceinfo',
              data: { sid: this.sid }
            }).then(res => {
              this.editForm = res.data.data[0]
              console.log(this.editForm)
              this.initPage()
              this.getQS()
              this.getCarNumbersBySid(this.sid)
            })

          })

        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    handleInputConfirm(row) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {

        let inputValue = this.inputValue;
        if (inputValue) {
          this.selectedCarNumbers.push(inputValue);
          axios({
            url: baseURL + '/source/insertsourcecarnumber',
            method: 'post',
            data: { sid: row.sid, carNumber: this.inputValue },
          }).then(res => {
            this.inputVisible = false;
            this.$message.success('')
          }).catch(err => {
            console.log(err)
            this.$message.error("")
          })
        }
        this.inputValue = '';

      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    showInput() {
      this.inputVisible = true;
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    editCarNumber(tag, row) {
      this.carNumberComplForm = tag
    },
    handleComplTime() {
      this.carNumberComplTimeVisible = false
      axios({
        url: baseURL + '/source/updatesourcecarnumber',
        method: 'post',
        data: this.carNumberComplForm,
      }).then(res => {
        this.getQS()
      }).catch(err => {

      })

    },
    exportSourceExcel() {
      let openUrl = `${baseURL}/source/exportsource?`;

      for (let obj in this.pageparm) {
        if (this.pageparm[obj]) {
          openUrl += obj + '=' + this.pageparm[obj] + '&'
        }
      }
      axios.get(openUrl).then(res => {
        window.location.href = openUrl
      })
    },
    exportSourceFinalExcel() {
      let openUrl = `${baseURL}/source/exportsourcefinalqualitysquare?`;

      for (let obj in this.pageparm || this.pageparm[obj] == 0) {
        if (this.pageparm[obj]) {
          openUrl += obj + '=' + this.pageparm[obj] + '&'
        }
      }
      axios.get(openUrl).then(res => {
        window.location.href = openUrl
      })
    },
    handleSourceExceed(files, fileList) {
      this.$message.warning(` ${files.length}  ${files.length + fileList.length} `);
    },
    handleSourceSuccess() {
      this.getQS()
      this.$message.success("")
    },
    handleError() {
      this.$message.error("")
    },
    handleCarNumbersInfo(row) {
      this.carNumbersInfoVisible = true
      this.getCarNumbersBySid(row.sid)
      this.sid = row.sid
      this.editForm = row
    },
    deleteCarNumbersInfo(row) {
      this.deleteCarNumbersInfoVisible = true
      this.getCarNumbersBySid(row.sid)
      this.sid = row.sid
      this.editForm = row
    },
    enditComplTime() {
      this.ComplTimeVisible = false
    },
    editComplTime(row) {
      axios({
        url: baseURL + '/source/updatesourcecarnumber',
        method: 'post',
        data: { cid: row.cid, complTime: row.complTime },
      }).then(res => {
        this.$message.success("")
        this.getQS()
      }).then(() => {
        this.updateNumber(this.sid)
      })
    },
    deleteSourceInfoBySID(sid) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {

        axios({
          url: baseURL + '/source/deletesourceinfosid',
          method: 'post',
          params: { sid }
        }).then(res => {
          this.$message.success("")
          this.getQS()
        }).catch(err => {
          this.$message.error("")
        })

      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    changeDimension(sid, dimension) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        axios({
          url: ` ${baseURL}/source/changedimension`,
          params: { sid, dimension }
        }).then(res => {
          this.$message.success("")
          this.search()
        }).catch(err => {
          this.$message.error('')
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    rowCarStyle({ row }) {
      if (row.concern == 1) {
        return { 'background': 'antiquewhite' }
      }
    },
    changeCarConcern(cid, concern) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        axios({
          url: ` ${baseURL}/source/changeconcern`,
          params: { cid, concern }
        }).then(res => {
          this.$message.success("")
          this.getQS()
          this.getCarNumbersBySid(this.sid)
          this.search()
        }).catch(err => {
          this.getQS()
          this.getCarNumbersBySid(this.sid)
          this.$message.error('')
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    closeCarNumbersInfoVisible() {
      this.carNumbersInfoVisible = false
      this.carNumbersInfoData = []
    },
    showModal(id) {
      console.log(id)
      // 
      axios({
        url: baseURL + '/source/fileList/' + id,
        method: 'get'
      }).then(res => {
        console.log(res)
        this.dialogVisible = true;
        this.attachmentList = res.data.data || []
      })
    },
    handleCloseFile(done) {
      // 
      done(); //  done() 
    },
    handleFileNameClick(row) {
      console.log(baseURL + '/source/download/' + row.sourceFileMidId)
      // 
      console.log('', row);
      // row.url
      // 
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        axios({
          url: baseURL + '/source/getFileInfo/' + row.fileId,
          method: 'get'
        }).then(res => {
          console.log(res)
          if (res.data.code !== 200) {
            this.$message.error(res.data.msg)
            this.showModal(row.sourceId);
          }
          downloadFile(baseURL + '/source/download/' + row.sourceFileMidId, row.fileName)
          console.log(baseURL + '/source/download/' + row.sourceFileMidId)
        }).catch(err => {
          this.$message.error(err)
        })
      })
    },
    downloadAttachment(attachment) {
      // 
      console.log('', attachment);
      //  window.open 
      downloadFile(baseURL + '/source/download/' + attachment.sourceFileMidId, attachment.fileName)
    },
    deleteAttachment(row) {
      // 
      console.log('', row);
      this.deleteParams = {
        fileId: row.fileId,
        sourceFileMidId: row.sourceFileMidId
      }
      // 
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        axios({
          url: baseURL + '/source/deleteFile',
          method: 'post',
          data: this.deleteParams
        }).then(res => {
          console.log(res)
          if (res.data.code !== 200) {
            this.$message.error(res.data.msg)
          } else {
            this.$message.success('!')
            this.showModal(row.sourceId)
          }
        }).catch(err => {
          this.$message.error(err)
        })
      })
    },
    showUploadModal(row) {
      this.source.sid = row.sid;
      this.uploadModalVisible = true;
    },
    beforeClose(done) {
      // 
      done();
    },
    handleFileSuccess(response, file, fileList) {
      console.log("success")
      if (response.code !== 200) {
        this.$message.error(response.msg)
        this.$refs.refName.uploadFiles.splice(this.$refs.refName.uploadFiles.indexOf(file), 1);
      } else {
        this.$message.success("")
      }
    },
    handleFileError(err, file, fileList) {
      this.$message.error('!')
      this.$refs.refName.uploadFiles.splice(this.$refs.refName.uploadFiles.indexOf(file), 1);
      this.$refs.refName.clearFiles();
    },
    upload() {
      // 
      this.$refs.refName.clearFiles();
      this.uploadModalVisible = false;
    }
  }
}
</script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.el-tag+.el-tag {
  margin-bottom: 10px;
}

.button-new-tag {
  margin-left: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}

.input-new-tag {
  width: 90px;
  margin-left: 10px;
  vertical-align: bottom;
}

.el-row {
  margin-bottom: 20px;
}

.el-col {
  border-radius: 4px;
}

.el-col-6 {
  width: 10%;
}

.bg-purple-light {
  background: #e5e9f2;
}

.grid-content {
  border-radius: 4px;
  min-height: 36px;
}

.row-bg {
  padding: 10px 0;
}

.input-new-tag {
  width: 150px !important;
}

.rate_wrap {
  display: flex;
  align-items: center;
}

.uploadFile {
  display: flex;
  align-items: center;
  justify-content: center;
}

/*  */
.uploadFile.drag-over {
  border-color: #409eff;
  /*  */
}

.icon-button {
  border: none;
  /*  */
  color: #409eff;
  /*  */
}

/* el-link */
.el-link {
  color: #409eff !important;
}
</style>
  


